
<template>
  <el-row class="warp">
    <el-col :span="24" class="itemname1" :loading="loading">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <span class="itemfont">系统日志</span>
          </el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <!-- <el-col :span="24">
        <div class="title">系统日志</div>
    </el-col> -->

     <el-col :span="24" class="sysloglist" v-loading="loading" element-loading-text="拼命加载中">

         <!--列表-->
      <el-table :data="systemlogs" border highlight-current-row v-loading="loading" style="width: 100%;border-radius:8px;" :header-cell-style="{background:'#98b3d4',color:'black'}">
        <el-table-column type="index" width="" >
        </el-table-column>
        <el-table-column prop="logtime" label="日志时间" :formatter="dateFormat" sortable>
        </el-table-column>
        <el-table-column prop="eventmessage" label="日志事件" width="" >
        </el-table-column>
        <el-table-column prop="username" label="用户名称" width="" >
        </el-table-column>
        <el-table-column prop="userip" label="用户IP" width="" >
        </el-table-column>
         
      </el-table>

    </el-col>
  </el-row>
</template>


<script>
 
  import API from '../../api/api_systemlog';
  import moment from "moment";

  export default{
    data(){
      return {
        value4: [new Date(2018, 10, 10, 10, 10), new Date(2018, 10, 11, 10, 10)],

        // filters: {
        //   name: ''
        // },
        
        loading: false,

        systemlogs: [{
          "logtime": 1491559642000
        }],

      }
    },
    methods: {
      //时间格式化  
      dateFormat:function(row, column) {  
          var date = row[column.property];  
          if (date == undefined) {  
              return "";  
          }
          return moment(date).format("YYYY-MM-DD HH:mm:ss");  
      },
 
      

      handleSearch(){
        this.search();
      },
      search(){
        let that = this;
        let params = {
         
        };
        that.loading = true;
        API.findList(params).then(function (result) {
          that.loading = false;
          if (result) {
            that.systemlogs = result.data;
          }
        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
        });
      },



    },
    mounted() {
      this.handleSearch()
    }
  }
</script>

<style>
.sysloglist{
  margin-top: 10px;
}

  .demo-table-expand label {
    font-weight: bold;
  }
</style>





