<template>
  <el-row class="warp">
    <el-col :span="24" class="itemname1">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b class="itemfont">首页</b></el-breadcrumb-item>
        <el-breadcrumb-item>
          <span class="itemfont">用户管理</span>
        </el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="userlist" v-loading="loading" element-loading-text="拼命加载中">
        <!--工具条-->
        <el-col :span="2" class="adduserbtn" style="padding-bottom: 0px;">
          <el-form :inline="true" :model="filters">
            <el-form-item v-if="rolename==0&&fenjiaoyuju==0">
              <el-button type="primary" @click="showAddDialog">新增用户</el-button>
            </el-form-item>
          </el-form>
        </el-col>

        <!--列表-->
      <el-table :data="users"  highlight-current-row v-loading="loading" style="width: 100%;border-radius:8px;" :header-cell-style="{background:'#98b3d4',color:'black'}">
        <el-table-column type="index" >
        </el-table-column>
        <el-table-column prop="loginname" label="登陆名称"  >
        </el-table-column>
        <el-table-column prop="username" label="真实姓名" >
        </el-table-column>
        <el-table-column prop="nickname" label="昵称"  >
        </el-table-column>
        <el-table-column prop="phone" label="手机号" >
        </el-table-column>
        <el-table-column prop="rolename"  :formatter="formatRole" label="权限" >
        </el-table-column>
        
        <el-table-column label="操作" min-width="150px">
           <template slot-scope="scope">
             <el-button size="small"  @click="showEditDialog(scope.$index,scope.row)">编辑</el-button>
             <el-button type="info" size="small" @click="showResetPassword(scope.$index,scope.row)">重置密码</el-button>
             <el-button type="danger" size="small" @click="delUser(scope.$index,scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>


       <!--新增界面-->
      <el-dialog title="新增用户" :visible.sync ="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="100px" :rules="addFormRules" ref="addForm">
          <el-form-item label="登陆名称" prop="loginname">
            <el-input v-model="addForm.loginname" auto-complete="off"></el-input>
          </el-form-item>

           <el-form-item label="登陆密码" prop="password">
            <el-input v-model="addForm.password" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="真实姓名" prop="username">
            <el-input v-model="addForm.username" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="昵称" prop="nickname">
            <el-input v-model="addForm.nickname" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="手机号" prop="phone">
            <el-input v-model="addForm.phone"></el-input>
          </el-form-item>

          <el-form-item label="单位权限" prop="rolename" v-if="rolename==0&&fenjiaoyuju==0">
            <el-select v-model="addForm.rolename"  placeholder="请选择" filterable>
              <el-option label="教育局" value="0" v-if="rolename==0&&fenjiaoyuju==0"></el-option>
              
              <el-option
                v-for="item in options_orgname"
                :key="item.value"
                :label="item.label1"
                :value="item.value1">
              </el-option>
            </el-select> 
          </el-form-item>

          <el-form-item label="分教育局权限" prop="fenjiaoyuju" v-if="rolename==0&&fenjiaoyuju==0">
            <el-select v-model="addForm.fenjiaoyuju"  placeholder="请选择">
              <el-option label="是" value="1"></el-option>
              <el-option label="否" value="0"></el-option>
            </el-select> 
          </el-form-item>

          <!-- <el-form-item label="单位权限" prop="rolename" v-if="rolename!=0">
            <el-input  :value="organname" disabled></el-input>
          </el-form-item> -->
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
        </div>
      </el-dialog>

      <!--编辑界面-->
      <el-dialog title="编辑" :visible.sync ="editFormVisible" :close-on-click-modal="false">
        <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">

          <el-form-item label="登陆名称" prop="loginname">
            <el-input v-model="editForm.loginname" auto-complete="off"></el-input>
          </el-form-item>

          <!--<el-form-item label="登陆密码" prop="password">
            <el-input v-model="editForm.password" auto-complete="off"></el-input>
          </el-form-item>-->

          <el-form-item label="真实姓名" prop="username">
            <el-input v-model="editForm.username" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="昵称" prop="nickname">
            <el-input v-model="editForm.nickname" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="手机号" prop="phone">
            <el-input v-model="editForm.phone"></el-input>
          </el-form-item>

        <!-- <el-form-item label="单位权限" prop="rolename" v-if="rolename==0">
            <el-select v-model="editForm.rolename" placeholder="请选择">
              <el-option
                v-for="item in options_orgname"
                :key="item.value"
                :label="item.label1"
                :value="item.value1">
              </el-option>
            </el-select> 
          </el-form-item> -->

          <el-form-item label="单位权限" prop="rolename" v-if="rolename==0&&fenjiaoyuju==0">
            <el-select v-model="editForm.rolename"  placeholder="请选择">
              <el-option label="教育局" value="0" v-if="rolename==0&&fenjiaoyuju==0"></el-option>
              
              <el-option
                v-for="item in options_orgname"
                :key="item.value"
                :label="item.label1"
                :value="item.value1">
              </el-option>
            </el-select> 
          </el-form-item>

          <el-form-item label="分教育局权限" prop="fenjiaoyuju" v-if="rolename==0&&fenjiaoyuju==0">
            <el-select v-model="editForm.fenjiaoyuju"  placeholder="请选择">
              <el-option label="是" value="1"></el-option>
              <el-option label="否" value="0"></el-option>
            </el-select> 
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="editFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="editSubmit">提交</el-button>
        </div>
      </el-dialog>


      <!--重置密码界面-->
      <el-dialog title="重置密码" :visible.sync ="resetFormVisible" :close-on-click-modal="false">
        <el-form :model="resetForm" label-width="100px"  ref="resetForm">
           <el-form-item label="登陆密码" prop="password">
            <el-input v-model="resetForm.password" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="resetFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="resetSubmit">提交</el-button>
        </div>
      </el-dialog>


     

      

    </el-col>
  </el-row>
</template>

<script>

  import API from '../../api/api_user';
  import organ from '../../api/api_organ';

  export default {
    data() {

      let logname = (rule, value, callback) => {
         if (!value) return callback(new Error('请输入用户名称'))
        // if (value.length > 50) return callback(new Error('序列号最多50个字符'))
        this.checkLoginName(value, callback)
        // 在此处将callback传到验证方法中
      }
      return {

        options_orgname:[],
        rolename:'',
        fenjiaoyuju:'',
        organname:'',
        filters: {
          name: ''
        },
        loading: false,
        users: [],
        
        loading: false,

        //新增相关数据
        addFormVisible: false,//新增界面是否显示
        addLoading: false,
        addFormRules: {
          loginname: [
            {validator: logname, trigger: 'blur'}
          ],

          // loginname: [
          //   {required: true, message: '请输入用户名称', trigger: 'blur'}
          // ],
          password: [
            {required: true, message: '请输入密码', trigger: 'blur'}
          ],
          username: [
            {required: true, message: '请输入真实姓名', trigger: 'blur'}
          ],
          nickname: [
            {required: true, message: '请输入昵称', trigger: 'blur'}
          ],
          phone: [
            {required: true, message: '请输入手机号', trigger: 'blur'}
          ]
        },
        addForm: {
          loginname: '',
          password: '',
          username: '',
          nickname: '',
          phone: ''  ,
          rolename: '',
          fenjiaoyuju: '',
          

        },

        //编辑相关数据
        editFormVisible: false,//编辑界面是否显示
        editFormRules: {
          

          loginname: [
            {required: true, message: '请输入用户名称', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '请输入密码', trigger: 'blur'}
          ],
          username: [
            {required: true, message: '请输入真实姓名', trigger: 'blur'}
          ],
          nickname: [
            {required: true, message: '请输入昵称', trigger: 'blur'}
          ],
          phone: [
            {required: true, message: '请输入电话', trigger: 'blur'}
          ]
          
        },
        editForm: {
          loginname: '',
          //password: '',
          username: '',
          nickname: '',
          phone: '',
          rolename: '',
          fenjiaoyuju: '',

        },

        resetFormVisible: false,//重置界面是否显示
        resetForm: {
          password: ''
        }

      }
    },
    methods: {

      //角色转换
    formatRole: function(row, column) {
      if(row.rolename==0&&row.fenjiaoyuju==0){
        return "教育局"
      }
      if(row.rolename==0&&row.fenjiaoyuju==1){
        return "分教育局"
      }
      if(row.rolename!=0){
        return "单位"
      }
      
    },

      
      handleSearch(){
        this.search();
      },

      //获取用户列表
      search: function () {
        let that = this;
        let user = JSON.parse(window.localStorage.getItem('access-user'))
        that.rolename=user.data.rolename
        that.fenjiaoyuju=user.data.fenjiaoyuju
        let params = {
          rolename:user.data.rolename,
          fenjiaoyuju:user.data.fenjiaoyuju,
          userid: user.data.userid
        };

        that.loading = true;
        API.findList(params).then(function (result) {
          that.loading = false;
          if (result) {
            that.users = result.data;
          }
        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
        });
      },

      //显示添加界面
      showAddDialog: function () {

        // let user = JSON.parse(window.localStorage.getItem('access-user'))
        // let params1={
        //   organid:user.data.rolename

        // };
        // let self= this;
        // organ.findList(params1).then(function (result) {
        //   self.organname = result.data.organname
          
        // });

        let user = JSON.parse(window.localStorage.getItem('access-user'))
        let params1={
          organid:user.data.rolename,
          fenjiaoyuju:user.data.fenjiaoyuju,
          userid: user.data.userid
        };
        let self= this;
        self.options_orgname = [];
        organ.findList(params1).then(function (result) {
          let x = result.data
          if (x) {
            x.forEach(item => {
              self.options_orgname.push({
                value1: item.id,
                label1: item.organname
              });
            });
          }
        });


        this.addFormVisible = true;
        //console.log(user.data.rolename);
        this.addForm = {
          loginname: '',
          password: '',
          username: '',
          nickname: '',
          phone: '',
          rolename: user.data.rolename,
        };
      },

      //新增
      addSubmit: function () {
        let that = this;
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            that.loading = true;
            let para = Object.assign({}, this.addForm);
        
            API.add(para).then(function (result) {
              that.loading = false;
              if (result) {
                that.$message.success({showClose: true, message: '新增成功', duration: 2000});
                that.$refs['addForm'].resetFields();
                that.addFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '修改失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });

          }
        });
      },


      //显示编辑界面
      showEditDialog: function (index, row) {

        let user = JSON.parse(window.localStorage.getItem('access-user'))
        let params1={
          organid:user.data.rolename,
          fenjiaoyuju:user.data.fenjiaoyuju,
          userid: user.data.userid
        };
        let self= this;
        self.options_orgname = [];
        organ.findList(params1).then(function (result) {
          let x = result.data
          if (x) {
            x.forEach(item => {
              self.options_orgname.push({
                value1: item.id,
                label1: item.organname
              });
            });
          }
        });


        this.editFormVisible = true;
        this.editForm = Object.assign({}, row);
      },
      //编辑
      editSubmit: function () {
        let that = this;
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.loading = true;
            let para = Object.assign({}, this.editForm);
            
            API.update(para.id, para).then(function (result) {
              that.loading = false;
              if (result) {
                that.$message.success({showClose: true, message: '修改成功', duration: 2000});
                that.$refs['editForm'].resetFields();
                that.editFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '修改失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });
          }
        });
      },


      //重置密码
      resetSubmit: function () {
        let that = this;
        this.$refs.resetForm.validate((valid) => {
          if (valid) {
            this.loading = true;
            let para = Object.assign({}, this.resetForm);
            
            API.reset(para.id, para).then(function (result) {
              that.loading = false;
              if (result) {
                that.$message.success({showClose: true, message: '修改成功', duration: 2000});
                that.$refs['resetForm'].resetFields();
                that.resetFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '修改失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });
          }
        });
      },


      //显示重置密码界面
      showResetPassword: function (index, row) {

        this.resetFormVisible = true;
        this.resetForm = Object.assign({}, row);
      },

      //删除
      delUser: function (index, row) {
        let that = this;
        let params = {
          userid:row.id
          
        };
        this.$confirm('确认删除该记录吗?', '提示', {type: 'warning'}).then(() => {
          that.loading = true;
          API.del(params).then(function (result) {
            that.loading = false;
            if (result) {
              that.$message.success({showClose: true, message: '删除成功', duration: 1500});
              that.search();
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            
          });
        }).catch(() => {
        });
      },


      checkLoginName(val, callback) {  // 此方法不能用async,原因未知
        
        this.users.forEach(item => {
            //console.log(item.jamip)
            if(val==item.loginname){
              console.log("这个登陆名被占用，换一个吧")
              callback(new Error('这个登陆名被占用，换一个吧'))
            }
    
          });
        
         callback()
      }






    },
    mounted() {
      this.handleSearch()
    }
  }
</script>

<style scoped>
.userlist{
  margin-top: 10px;
}
</style>



