<template>
  <el-row class="warp">
    <!-- <el-col :span="24" class="warp-breadcrum"> -->
      <div class="itemname1">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item >
            <b class="itemfont">单位</b>
          </el-breadcrumb-item>
        </el-breadcrumb>
      </div>
    <!-- </el-col> -->

      <section class="chart-container">
        <el-row>

          <!--教育局 -->
          <el-col :span="4" v-for="(item,index) in organList" :key="index">

            <div @click="showbuilding(item.value,item.label)" class="jammcard" v-if="rolename==0">
            <!--<el-card :body-style="{ padding: '10px'}" @click.native="showbuilding(item.value,item.label)" class="jammcard" v-if="rolename==0">-->
              <img src="../assets/images/xuexiaologo.png" class="image">
              <!--<div style="padding: 14px;">-->
              <div class="schoolname">
                <span>{{item.label}}</span>
              </div>
            <!--</el-card>-->
            </div>
          </el-col>

          <!--单位 -->
          <!--<el-col :span="4">
            <el-card :body-style="{ padding: '10px'}" @click.native="showbuilding(orgid,orgname)" class="jammercard" v-if="rolename!=0">
              <img src="../assets/images/school22.jpg" class="image">
              <div style="text-align:center;padding: 1px;">
                <span>{{orgname}}</span>
              </div>
            </el-card>
          </el-col>-->

        </el-row>
      </section>

  </el-row>
</template>


<script>
  //import echarts from "echarts";
  import organ from '../api/api_organ';

  export default {
    data() {
      return {
        organList: [],
        orgid:'',
        orgname:'',
        rolename:''
      }
    },
    mounted: function () {
      let self = this;
      let user = JSON.parse(window.localStorage.getItem('access-user'))
      self.rolename=user.data.rolename
      let params = {
        organid:user.data.rolename,
        fenjiaoyuju:user.data.fenjiaoyuju,
        userid: user.data.userid
      };
      
      organ.findList(params).then(function (result) {
        let x = result.data

        if (user.data.rolename==0) {
          x.forEach(item => {
            self.organList.push({
              value: item.id,
              label: item.organname
            });
          });
        }else{
          self.orgid=x.id
          self.orgname=x.organname
          self.showbuilding(x.id,x.organname)
        }


        // if (x.length>=2) {
        //   x.forEach(item => {
        //     self.organList.push({
        //       value: item.id,
        //       label: item.organname
        //     });
        //   });
        // }else{
        //   self.orgid=x.id
        //   self.orgname=x.organname
        //   self.showbuilding(x.id,x.organname)
        // }




      });
    },
    methods: {
      showbuilding(x,y) {
        this.$router.push({
          path: "/building/info",
          query: {
            id: x,
            organname:y
          }
        });
      }
    }
  };
  

</script>

<style>

.itemname1{
  margin-top: 40px;
}
.itemfont{
  color: white
}

.jammcard{
  min-height: 200px;
  min-width: 200px;
  width: 20%;
}

  .chart-container .el-col {
    padding: 30px 20px;
    cursor:pointer;
  }

  /* .jammcard:hover{
    background-color:#5f6d6a;
  } */

</style>



