/**
 * 
 * 单位管理相关api
 */
import * as API from './'

export default {

  //查询获取单位列表
  findList: params => {
    return API.POST('/organ/list',params)
  },

  findFen:params => {
    return API.POST('/user/fenjiaoyu',params)
  },

  add: params => {
    return API.POST(`/organ/add`, params)
  },
  
  update: (id, params) => {
    return API.POST(`/organ/update`, params)
  },

  //单个删除单位
  remove: params => {
    return API.POST(`/organ/del`,params)
  }

  //批量删除，传ids数组
//   removeBatch: (ids) => {
//     return API.DELETE(`/api/v1/books/batch/${ids}`)
//   }

}



