<template>
  <el-row class="warp">
    <!-- <el-col :span="24" class="warp-breadcrum"> -->
    <div class="itemname1">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b class="itemfont">首页</b></el-breadcrumb-item>
        <el-breadcrumb-item>
          <span class="itemfont">型号管理</span>
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- </el-col> -->

  <section class="chart-container">
    <!-- <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="拼命加载中"> -->
      
      <!--新增按钮-->
      <!-- <el-col :span="24"  style="padding-bottom: 0px;">
        <el-form>
          <el-form-item>
            <el-button type="primary" @click="showAddDialog">新增型号</el-button>
          </el-form-item>
        </el-form>
      </el-col> -->
      <div style="padding:10px">
        <el-button type="primary" @click="showAddDialog">新增型号</el-button>
      </div>


      <!--列表-->
      <el-table :data="data" highlight-current-row v-loading="loading" style="width: 100%;border-radius:8px;" :header-cell-style="{background:'#98b3d4',color:'black'}">
        <el-table-column type="index" width="">
        </el-table-column>
        <el-table-column prop="version" label="型号名称" >
        </el-table-column>

        <el-table-column prop="isadjust" label="是否调节" :formatter="formatRole">
        </el-table-column>
        
        
        
        <el-table-column label="操作" width="500">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditDialog(scope.$index,scope.row)">编辑</el-button> 
            <el-button type="danger" size="small" @click="delVersion(scope.$index,scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--编辑界面-->
      <el-dialog title="编辑" :visible.sync ="editFormVisible" :close-on-click-modal="false">
        <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
          <el-form-item label="型号名称" prop="version">
            <el-input v-model="editForm.version" auto-complete="off"></el-input>
          </el-form-item>


           <!--<el-form-item label="是否调节" prop="isadjust" >
            <el-select v-model="editForm.isadjust" placeholder="请选择">
              <el-option label="是" value="1"></el-option>
              <el-option label="否" value="0"></el-option>
            </el-select> 
          </el-form-item>-->

          
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="editFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="editSubmit">提交</el-button>
        </div>
      </el-dialog>


      <!--新增界面-->
      <el-dialog title="新增型号" :visible.sync ="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="100px" :rules="addFormRules" ref="addForm">

          <el-form-item label="型号名称" prop="version">
            <el-input v-model="addForm.version" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="是否调节" prop="isadjust" >
            <el-select v-model="addForm.isadjust" placeholder="请选择">
              <el-option label="是" value="1"></el-option>
              <el-option label="否" value="0"></el-option>
            </el-select> 
          </el-form-item>

          
         
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
        </div>
      </el-dialog>

      <!--
      <el-select v-model="value" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>  -->

    <!-- </el-col> -->
  </section>
  </el-row>
</template>

<script>
  import API from '../../api/api_version';

  export default {
    data() {
      return {
        filters: {
          name: ''
        },
        loading: false,
        data: [],
        // total: 0,
        // page: 1,
        // limit: 10,
        loading: false,


        //编辑相关数据
        editFormVisible: false,//编辑界面是否显示
        editFormRules: {
          version: [
            {required: true, message: '请输入型号名称', trigger: 'blur'}
          ]
          
          
        },
        editForm: {
          version: '',
          isadjust: ''
          
        },

        //新增相关数据
        addFormVisible: false,//新增界面是否显示
        addLoading: false,
        addFormRules: {

          version: [
            {required: true, message: '请输入型号名称', trigger: 'blur'}
          ]

        },
        addForm: {
          version: '',
          isadjust: ''
         
          
        }
      }
    },
    methods: {

       formatRole: function(row, column) {
            return row.isadjust == '1' ? "是" :  "否";
       },
      //性别显示转换
      // formatSex: function (row, column) {
      //   return row.sex == 1 ? '男' : row.sex == 0 ? '女' : '未知';
      // },
      // handleCurrentChange(val) {
      //   //this.page = val;
      //   this.search();
      // },
      handleSearch(){
        // this.total = 0;
        // this.page = 1;
        this.search();
      },
      //获取用户列表
      search: function () {
        let that = this;
        let params = {
          
        };

        that.loading = true;
        API.findList(params).then(function (result) {
          that.loading = false;
          if (result && result.data) {
            that.data = result.data;
          }
        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
        });
      },

      showAddDialog: function () {
        this.addFormVisible = true;
        this.addForm = {
          version: '',
          isadjust: ''
         
        };
      },
      //新增
      addSubmit: function () {
        let that = this;
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            that.loading = true;
            let para = Object.assign({}, this.addForm);
        
            API.add(para).then(function (result) {
              that.loading = false;
              if (result) {
                that.$message.success({showClose: true, message: '新增成功', duration: 2000});
                that.$refs['addForm'].resetFields();
                that.addFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '修改失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });

          }
        });
      },


      //显示编辑界面
      showEditDialog: function (index, row) {
        this.editFormVisible = true;
        this.editForm = Object.assign({}, row);
      },
      //编辑
      editSubmit: function () {
        let that = this;
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.loading = true;
            let para = Object.assign({}, this.editForm);
            
            API.update(para.id, para).then(function (result) {
              that.loading = false;
              if (result) {
                that.$message.success({showClose: true, message: '修改成功', duration: 2000});
                that.$refs['editForm'].resetFields();
                that.editFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '修改失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });
          }
        });
      },


      //删除
      delVersion: function (index, row) {
        let that = this;
        let params = {
          id:row.id
          
        };
        this.$confirm('确认删除该记录吗?', '提示', {type: 'warning'}).then(() => {
          that.loading = true;
          API.remove(params).then(function (result) {
            that.loading = false;
            if (result) {
              that.$message.success({showClose: true, message: '删除成功', duration: 1500});
              that.search();
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            
          });
        }).catch(() => {
        });
      }
    },
    mounted() {
      this.handleSearch()
    }
  }
</script>

<style scoped>

</style>


