
<template>
  <el-row class="warp">
    

   <el-col :span="24" class="itemname1">
        <div class="itemfont">定时计划</div>
    </el-col>

    <!--新增按钮-->
      <el-col :span="24" class="addtimebtn" style="padding-bottom: 0px;">
        <el-form>
          <el-form-item>
            <el-button type="primary" @click="showAddDialog">按天定时</el-button>
          </el-form-item>
        </el-form>
      </el-col>

    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="拼命加载中">

         <!--列表-->
      <el-table :data="timeplans"  highlight-current-row v-loading="loading" style="width: 100%;border-radius:8px;" :header-cell-style="{background:'#98b3d4',color:'black'}">
        <el-table-column type="index" width="" >
        </el-table-column>
        <el-table-column prop="organ.organname" label="定时单位">
        </el-table-column>
        <el-table-column prop="devopentime" label="开机时间" :formatter="dateFormat" sortable>
        </el-table-column>
        <el-table-column prop="devclosetime" label="关机时间" :formatter="dateFormat" sortable>
        </el-table-column>
        <el-table-column label="操作" width="">
           <template slot-scope="scope">
             <el-button type="danger" size="small" @click="delTime(scope.$index,scope.row)">删除</el-button>
          </template>
         </el-table-column>
      </el-table>

       <!--新增界面-->
      <el-dialog title="新增定时" :visible.sync ="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="100px" :rules="addFormRules" ref="addForm">

         <el-form-item label="所属单位" prop="orgid" v-if="rolename==0">
            <el-select v-model="addForm.orgid" placeholder="请选择">
              <el-option
                v-for="item in options_orgname"
                :key="item.value"
                :label="item.label1"
                :value="item.value1">
              </el-option>
            </el-select> 
          </el-form-item>

          <el-form-item label="单位" prop="orgid" v-if="rolename!=0">
            <el-input  :value="organname" disabled></el-input>
          </el-form-item>

           <el-form-item label="开机时间" prop="devopentime">
            <el-date-picker
                v-model="addForm.devopentime"
                type="datetime"
                placeholder="选择日期时间">
            </el-date-picker>
          </el-form-item>

          

          <el-form-item label="关机时间" prop="devclosetime">
             <el-date-picker
                v-model="addForm.devclosetime"
                type="datetime"
                placeholder="选择日期时间">
            </el-date-picker>
          </el-form-item>

         
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
        </div>
      </el-dialog>

    </el-col>
  </el-row>
</template>


<script>

  import time from '../../api/api_time';
  import organ from '../../api/api_organ';
  import moment from "moment";

  export default {
    data() {
      return {

        filters: {
          name: ''
        },

        rolename:'',
        fenjiaoyuju:'',
        organname: '',
        loading: false,
        timeplans: [{
          "devopentime": 1491559642000,
          "devclosetime": 1491559642000,

        }],

        options_orgname:[],
        
        loading: false,

        //新增相关数据
        addFormVisible: false,//新增界面是否显示
        addLoading: false,
        addFormRules: {

          organname: [
            {required: true, message: '请输入定时单位', trigger: 'blur'}
          ]
          
        },
        addForm: {
          orgid: '',
          devopentime: '',
          devclosetime: ''
        },

      }
    },
    methods: {
      //时间格式化  
      dateFormat:function(row, column) {  
          var date = row[column.property];  
          if (date == undefined) {  
              return "";  
          }
          return moment(date).format("YYYY-MM-DD HH:mm:ss");  
      },
      
      handleSearch(){
        this.search();
      },

      //获取用户列表
      search: function () {
        let that = this;
        let user = JSON.parse(window.localStorage.getItem('access-user'))
        that.rolename=user.data.rolename
        let params = {
          id: user.data.rolename,
          fenjiaoyuju:user.data.fenjiaoyuju,
          userid: user.data.userid
        };

        that.loading = true;
        time.findList(params).then(function (result) {
          that.loading = false;
          if (result) {
            that.timeplans = result.data;
          }
        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
        });
      },


      //定时功能
      dingshi: function () {
        let that = this;
        let params = {
          
        };
        that.loading = true;
        time.dingshi(params).then(function (result) {
       
        })
      },

      
      //显示添加界面
      showAddDialog: function () {

        let user = JSON.parse(window.localStorage.getItem('access-user'))
        let params1={
          organid:user.data.rolename,
          fenjiaoyuju:user.data.fenjiaoyuju,
          userid: user.data.userid
        };
        let self= this;
        self.options_orgname = [];
        organ.findList(params1).then(function (result) {
          let x = result.data
          if (user.data.rolename==0) {
            x.forEach(item => {
              self.options_orgname.push({
                value1: item.id,
                label1: item.organname
              });
            });
          }else{
            self.organname = x.organname
          }
        });


        this.addFormVisible = true;
        this.addForm = {
          orgid: user.data.rolename,
          devopentime: '',
          devclosetime: '',
          userid: user.data.userid
          
        };
      },

      //新增
      addSubmit: function () {
        let that = this;
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            that.loading = true;
            let para = Object.assign({}, this.addForm);
        
            time.add(para).then(function (result) {
              that.loading = false;
              if (result) {
                that.$message.success({showClose: true, message: '新增成功', duration: 2000});
                that.$refs['addForm'].resetFields();
                that.addFormVisible = false;
                that.search();
                that.dingshi();
              } else {
                that.$message.error({showClose: true, message: '修改失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });

          }
        });
      },


      //删除定时
      delTime: function (index, row) {
        let that = this;
        let params = {
          id:row.id
          
        };
        this.$confirm('确认删除该记录吗?', '提示', {type: 'warning'}).then(() => {
          that.loading = true;
          time.del(params).then(function (result) {
            that.loading = false;
            if (result) {
              that.$message.success({showClose: true, message: '删除成功', duration: 1500});
              that.search();
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            
          });
        }).catch(() => {
        });
      }


   

    },
    mounted() {
      this.handleSearch()
    }
  }
</script>

<style scoped>
.addtimebtn{
  margin-top: 10px;
}
</style>











