<template>
  <el-row class="warp">
    <el-col :span="24" class="itemname1">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <b class="itemfont">所属单位</b>
        </el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>
    <el-col class="loudongmanager">
        <div class="title">楼栋管理</div>
    </el-col>

    <!-- <section class="chart-container"> -->
      <el-row>

        <!--教育局 -->
        <!-- <el-col :span="3" v-for="(item,index) in organList" :key="index" > -->
          <div v-for="(item,index) in organList" :key="index" class="jiaoyuju">
            <!-- <el-card :body-style="{ padding: '5px'}" @click.native="showBuildingByOrgan(item.value,item.label)" class="jammercard" v-if="rolename==0"> -->
              <div @click="showBuildingByOrgan(item.value,item.label)" class="jammer" v-if="rolename==0">
              <img src="../../assets/images/school3.png" class="image">
              <!--<div style="padding: 14px;">-->
              <div style="text-align:center;padding: 1px;">
                  <span class="itemfont">{{item.label}}</span>
              </div>
              </div>
            <!-- </el-card> -->
          </div>
        <!-- </el-col> -->

        <!--单位 -->
        <!--<el-col :span="3"  >
            <el-card :body-style="{ padding: '10px'}" @click.native="showBuildingByOrgan(orgid,orgname)" class="jammercard" v-if="rolename!=0">
              <img src="../../assets/images/school3.png" class="image">
              <div style="text-align:center;padding: 1px;">
                  <span>{{orgname}}</span>
              </div>
            </el-card>
        </el-col>-->
      </el-row>
    <!-- </section> -->


  </el-row>
</template>


<script>
//import echarts from "echarts";
import organ from "../../api/api_organ";

export default {
  data() {
    return {
      organList: [],
      orgid:'',
      orgname:'',
      rolename:''
    };
  },
  mounted: function() {
    
    let self = this;
    let user = JSON.parse(window.localStorage.getItem('access-user'))
    self.rolename=user.data.rolename

    let params = {
      organid:user.data.rolename,
      fenjiaoyuju:user.data.fenjiaoyuju,
      userid: user.data.userid
    };
    organ.findList(params).then(function(result) {
      let x = result.data;
      if (user.data.rolename==0) {
        x.forEach(item => {
          self.organList.push({
            value: item.id,
            label: item.organname
          });
        });
      }else{
        self.orgid=x.id
        self.orgname=x.organname
        self.showBuildingByOrgan(x.id,x.organname)
      }
    });
  },
  methods: {
    showBuildingByOrgan(x, y) {
      this.$router.push({
        path: "/building/list",
        query: {
          id: x,
          organname: y
        }
      });
    }
  }
};

</script>


<style scoped>


  .itemfont{
    color: white;
    /* font-family: "微软雅黑" */

  }
  .loudongmanager{
    margin-top: 10px;

  }

  .jammer{
    width: 100%;
    min-height: 160px;
    min-width: 100px;
  
  }

  @media (min-width: 0px) {
    .jammer {
      width: 50%;
      float: left;
      height: auto;
      /* padding-bottom: 5rem; */
      padding: 2rem;
    }
}

@media (min-width: 800px) {
    .jammer {
      width: 30%;
      float: left;
      height: auto;
      /* padding-bottom: 5rem; */
      /* padding: 2rem; */
    }
  }
  @media (min-width: 1100px) {
    .jammer {
      width: 20%;
      float: left;
      height: auto;
      /* padding-bottom: 5rem; */
      /* padding: 2rem; */
    }
  }

  @media (min-width: 1300px) {
    .jammer {
      width: 15%;
      float: left;
      height: auto;
      /* padding-bottom: 5rem; */
      /* padding: 2rem; */
    }
  }


  @media (min-width: 1500px) {
    .jammer {
      width: 15%;
      float: left;
      height: auto;
      /* overflow-y: scroll; */
      /* padding: 2rem; */
      /* padding-left: 3rem;
      padding-bottom: 3rem; */
      
    }
  }

  @media (min-width: 1600px) {
    .jammer {
      width: 10%;
      float: left;
      height: auto;
      /* overflow-y: scroll; */
      /* padding: 1rem; */
      /* padding-left: 3rem;
      padding-bottom: 3rem; */
      
    }
  }


  .title{
    height:20px;
    border-bottom: 1px solid #D3dce6;
    background-color: #F9FAFC;
    padding:10px;
    text-align: center;
  }
  /* .chart-container .el-col {
    padding: 20px 20px;
    cursor: pointer;
  } */
  /* .jammercard:hover{
    background-color:#5f6d6a;
  } */


</style>


