/**
 * 
 * 楼栋管理相关api
 */
import * as API from './'

export default {

  //查询获取单位列表
  findList: params => {
    return API.POST('/version/list',params)
  },

  add: params => {
    return API.POST(`/version/add`, params)
  },
  
  update: (id, params) => {
    return API.POST(`/version/update`, params)
  },

  //单个删除单位
  remove: params => {
    return API.POST(`/version/del`,params)
  }

  //批量删除，传ids数组
//   removeBatch: (ids) => {
//     return API.DELETE(`/api/v1/books/batch/${ids}`)
//   }

}



