<template>
  <el-row class="warp">
    <el-col :span="24" class="itemname1">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <span class="itemfont">报警查询</span>
        </el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

  <section class="alarmbody">
    <el-col :span="24" class="warp-breadcrum">
      <el-button type="text" size="small">日期</el-button>
      <el-date-picker value-format="yyyy-MM-dd" v-model="searchData" type="daterange" size="small" align="center" unlink-panels range-separator="至" start-placeholder="开始日期"
          end-placeholder="结束日期" :picker-options="dataPickerOption" @change="searchTime">
      </el-date-picker>

      <el-button type="text" size="small">报警信息</el-button>
      <el-select  v-model="selectvalue" placeholder="请选择" @change="selectStatus">
        <el-option label="设备恢复正常" value="1"></el-option>
        <el-option label="设备异常"     value="0"></el-option>
      </el-select>
    </el-col>

    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="拼命加载中">
      <!--列表-->
      <el-table :data="alarms" highlight-current-row style="width: 100%;border-radius:8px;"  :header-cell-style="{background:'#98b3d4',color:'black'}"> 
        <el-table-column type="index" width="60"></el-table-column>
        <el-table-column prop="deviceno" label="设备编号" ></el-table-column>
        <el-table-column prop="devicemac" label="设备MAC" ></el-table-column>
        <el-table-column prop="building.organ.organname" label="单位" ></el-table-column>
        <el-table-column prop="building.buildingname" label="楼栋" ></el-table-column>
        <el-table-column prop="locationinfo" label="位置" ></el-table-column>
        <el-table-column prop="alarminfo" label="报警信息" :formatter="formatRole"></el-table-column>
        <el-table-column prop="alarmtime" label="报警时间" :formatter="dateFormat" sortable></el-table-column>
      </el-table>  
    </el-col>
  </section>
  </el-row>
</template>
<script>
 
  import API from '../../api/api_alarm';
  import moment from "moment";

  export default{
    data(){
      return {
        value4: [new Date(2018, 10, 10, 10, 10), new Date(2018, 10, 11, 10, 10)],

        // filters: {
        //   name: ''
        // },
        
        loading: false,

        searchData: '',
        selectvalue: '',
        
        dataPickerOption: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },

        alarms: [{
          "alarmtime": 1491559642000
        }],

        
        

      }
    },


    watch: {
      searchData(val, oldVal) {
        //console.log(val)
      }

    },
    mounted() {
      this.handleSearch()
    },




    methods: {
      //时间格式化  
      dateFormat:function(row, column) {  
          var date = row[column.property];  
          if (date == undefined) {  
              return "";  
          }
          return moment(date).format("YYYY-MM-DD HH:mm:ss");  
      },

      formatRole: function(row, column) {
            return row.alarminfo == '1' ? "设备恢复正常" :  "设备异常";
       },
 
      

      handleSearch(){
        this.search();
      },
      search(){
        let that = this;
        let user = JSON.parse(window.localStorage.getItem('access-user'))
        let params = {
          orgid:user.data.rolename,
          fenjiaoyuju:user.data.fenjiaoyuju,
          userid: user.data.userid
          
        };
        that.loading = true;
        API.findList(params).then(function (result) {
          that.loading = false;
          if (result) {
            that.alarms = result.data;
          }
        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
        });
      },


      datesearch(val){
        let that = this;
        let user = JSON.parse(window.localStorage.getItem('access-user'))
        let params = {
          userid: user.data.userid,
          starttime:val[0],
          endtime:val[1]
        };
        that.loading = true;
        API.findTime(params).then(function (result) {
          that.loading = false;
          if (result) {
            that.alarms = result.data;
          }
        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
        });
      },





      searchTime(val){
        console.log("h",val)
        this.datesearch(val);

        // let params = {
        //   starttime:val[0],
        //   endtime:val[1]
        // };
        // that.loading = true;
        // API.findTime(params).then(function (result) {
        //   that.loading = false;
        //   if (result) {
        //     that.alarms = result.data;
        //   }
        // }, function (err) {
        //   that.loading = false;
        //   that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        // }).catch(function (error) {
        //   that.loading = false;
        //   console.log(error);
        //   that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
        // });



      },

      

      //查询报警状态
      selectStatus(value){
        //console.log("选中",value)
        //this.jammerStatus(value)
        let that = this;
        let user = JSON.parse(window.localStorage.getItem('access-user'))
        let params = {
          userid: user.data.userid,
          alarminfo:value
          
        };
        that.loading = true;
        API.findStatus(params).then(function (result) {
          that.loading = false;
          if (result) {
            that.alarms = result.data;
          }
        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
        });

        
      }
      


    },

    
  }
</script>

<style>

.alarmbody{
  margin-top: 10px;
}

  /* .demo-table-expand label {
    font-weight: bold;
  } */

  /* .title{
    height:20px;
    border-bottom: 1px solid #D3dce6;
    background-color: #F9FAFC;
    padding:10px;
    text-align: center;
  } */
</style>



