<template>
  <el-row class="warp">
    <el-col :span="24" class="itemname1">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b class="itemfont">首页</b></el-breadcrumb-item>
        <el-breadcrumb-item>
          <span class="itemfont">单位管理</span>
          </el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <!--教育局权限-->
    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="拼命加载中">
      
      <!--新增按钮-->
      <el-col :span="24" class="addorganbtn" style="padding-bottom: 0px;">
        <el-form>
          <el-form-item>
            <el-button type="primary" @click="showAddDialog">新增单位</el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!--教育局列表-->
      <el-table :data="data"  highlight-current-row v-loading="loading" style="width: 100%;border-radius:8px;" v-if="rolename==0&&fenjiaoyuju==0" :header-cell-style="{background:'#98b3d4',color:'black'}">
        <el-table-column type="index" width=""></el-table-column>
        <el-table-column prop="organname" label="单位名称" width="" >
        </el-table-column>
        <el-table-column prop="address" label="单位地址" width="" >
        </el-table-column>
        <el-table-column prop="managername" label="管理员姓名" width="" >
        </el-table-column>
        <el-table-column prop="managerphone" label="管理员电话" width="" >
        </el-table-column>
        <el-table-column prop="remark" label="备注" width="" >
          </el-table-column>
        <el-table-column prop="users.loginname" label="分教育局用户" width="" >
        </el-table-column>
        
        <el-table-column label="操作" width="300">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditDialog(scope.$index,scope.row)">编辑</el-button> 
            <el-button type="danger" size="small" @click="delOrgan(scope.$index,scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- <el-table :data="data"  highlight-current-row v-loading="loading" style="width: 100%;border-radius:8px;" v-if="rolename==0" :header-cell-style="{background:'#98b3d4',color:'black'}">
        <el-table-column type="index" width=""></el-table-column>
        <el-table-column prop="organname" label="单位名称" width="" >
        </el-table-column>
        <el-table-column prop="address" label="单位地址" width="" >
        </el-table-column>
        <el-table-column prop="managername" label="管理员姓名" width="" >
        </el-table-column>
        <el-table-column prop="managerphone" label="管理员电话" width="" >
        </el-table-column>
        <el-table-column prop="remark" label="备注" width="" >
        </el-table-column>
        
        <el-table-column label="操作" width="300">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditDialog(scope.$index,scope.row)">编辑</el-button> 
            <el-button type="danger" size="small" @click="delOrgan(scope.$index,scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table> -->

      <!--分教育局列表-->
      <el-table :data="data"  highlight-current-row v-loading="loading" style="width: 100%;border-radius:8px;" v-if="rolename==0&&fenjiaoyuju==1" :header-cell-style="{background:'#98b3d4',color:'black'}">
        <el-table-column type="index" width=""></el-table-column>
        <el-table-column prop="organname" label="单位名称" width="" >
        </el-table-column>
        <el-table-column prop="address" label="单位地址" width="" >
        </el-table-column>
        <el-table-column prop="managername" label="管理员姓名" width="" >
        </el-table-column>
        <el-table-column prop="managerphone" label="管理员电话" width="" >
        </el-table-column>
        <el-table-column prop="remark" label="备注" width="" >
        </el-table-column>
        
        <el-table-column label="操作" width="500">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditDialog(scope.$index,scope.row)">编辑</el-button> 
            <el-button type="danger" size="small" @click="delOrgan(scope.$index,scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>



      <!--单位列表-->
      <!--<el-table :data="organ" border highlight-current-row v-loading="loading" style="width: 100%;" v-if="rolename!=0">
        <el-table-column type="index" width=""></el-table-column>
        <el-table-column prop="organname" label="单位名称" width="" >
        </el-table-column>
        <el-table-column prop="address" label="单位地址" width="" >
        </el-table-column>
        <el-table-column prop="managername" label="管理员姓名" width="" >
        </el-table-column>
        <el-table-column prop="managerphone" label="管理员电话" width="" >
        </el-table-column>
        <el-table-column prop="remark" label="备注" width="" >
        </el-table-column>
        
        <el-table-column label="操作" width="500">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditDialog(scope.$index,scope.row)">编辑</el-button> 
            <el-button type="danger" size="small" @click="delOrgan(scope.$index,scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>-->


 

      <!--编辑界面-->
      <el-dialog title="编辑" :visible.sync ="editFormVisible" :close-on-click-modal="false">
        <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
          <el-form-item label="单位名称" prop="organname">
            <el-input v-model="editForm.organname" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="单位地址" prop="address">
            <el-input v-model="editForm.address" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="管理员姓名" prop="managername">
            <el-input v-model="editForm.managername" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="管理员电话" prop="managerphone">
            <el-input v-model="editForm.managerphone" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="备注" prop="remark">
            <el-input v-model="editForm.remark" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="分教育局用户" prop="userid" v-if="rolename==0&&fenjiaoyuju==0">
            <el-select v-model="editForm.userid"  placeholder="请选择">
              
              <el-option
                v-for="item in options_fenjiaoyuju"
                :key="item.value"
                :label="item.label1"
                :value="item.value1">
              </el-option>
            </el-select> 
          </el-form-item>
      
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="editFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="editSubmit">提交</el-button>
        </div>
      </el-dialog>


      <!--新增界面-->
      <el-dialog title="新增单位" :visible.sync ="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="100px" :rules="addFormRules" ref="addForm">

          <el-form-item label="单位名称" prop="organname">
            <el-input v-model="addForm.organname" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="单位地址" prop="address">
            <el-input v-model="addForm.address" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="管理员姓名" prop="managername">
            <el-input v-model="addForm.managername" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="管理员电话" prop="managerphone">
            <el-input v-model="addForm.managerphone"></el-input>
          </el-form-item>

          <el-form-item label="备注" prop="remark">
            <el-input v-model="addForm.remark"></el-input>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
        </div>
      </el-dialog>

      <!--
      <el-select v-model="value" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>  -->

    </el-col>

    <!--单位权限-->
    <!--<el-col>
      <h1>您没有管理单位的权限</h1>
    </el-col>-->
  </el-row>
</template>

<script>
  import API from '../../api/api_organ';

  export default {
    data() {
      return {
        filters: {
          name: ''
        },
        loading: false,
        rolename:'',
        fenjiaoyuju:'',
        data: [],
        organ: [],
        options_fenjiaoyuju: [],
        
        // total: 0,
        // page: 1,
        // limit: 10,
        loading: false,


        //编辑相关数据
        editFormVisible: false,//编辑界面是否显示
        editFormRules: {
          organname: [
            {required: true, message: '请输入单位名称', trigger: 'blur'}
          ],
          address: [
            {required: true, message: '请输入单位地址', trigger: 'blur'}
          ],
          managername: [
            {required: true, message: '请输入管理员姓名', trigger: 'blur'}
          ],
          managerphone: [
            {required: true, message: '请输入管理员电话', trigger: 'blur'}
          ],
          remark: [
            {required: true, message: '请输入备注', trigger: 'blur'}
          ]
          
        },
        editForm: {
          organname: '',
          address: '',
          managername: '',
          managerphone: '',
          remark: '',
          userid: '',
        },

        //新增相关数据
        addFormVisible: false,//新增界面是否显示
        addLoading: false,
        addFormRules: {

          organname: [
            {required: true, message: '请输入单位名称', trigger: 'blur'}
          ],
          address: [
            {required: true, message: '请输入单位地址', trigger: 'blur'}
          ],
          managername: [
            {required: true, message: '请输入管理员姓名', trigger: 'blur'}
          ],
          managerphone: [
            {required: true, message: '请输入管理员电话', trigger: 'blur'}
          ],
          remark: [
            {required: true, message: '请输入备注', trigger: 'blur'}
          ]
          

        },
        addForm: {
          organname: '',
          address: '',
          managername: '',
          managerphone: '',
          remark: ''
          
        }
      }
    },
    methods: {

      //性别显示转换
      // formatSex: function (row, column) {
      //   return row.sex == 1 ? '男' : row.sex == 0 ? '女' : '未知';
      // },
      // handleCurrentChange(val) {
      //   //this.page = val;
      //   this.search();
      // },
      handleSearch(){
        // this.total = 0;
        // this.page = 1;
        this.search();
      },
      //获取单位列表
      search: function () {
        let that = this;
        let user = JSON.parse(window.localStorage.getItem('access-user'))
        that.rolename=user.data.rolename
        that.fenjiaoyuju=user.data.fenjiaoyuju
        let params = {
          organid:user.data.rolename,
          fenjiaoyuju:user.data.fenjiaoyuju,
          userid: user.data.userid
        };

        that.loading = true;
        API.findList(params).then(function (result) {
          that.loading = false;
          if (result) {
            that.data = result.data;
            // that.data.push({
            //   organname:result.data.organname
            // })
          }
          // else{
          //   that.organ.push({
          //     organname:result.data.organname,
          //     address:result.data.address,
          //     managername:result.data.managername,
          //     managerphone:result.data.managerphone,
          //     remark:result.data.remark
          //   })
          // }

        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
        });
      },

      showAddDialog: function () {
        this.addFormVisible = true;
        this.addForm = {
          organname: '',
          address: '',
          managername: '',
          managerphone: '',
          remark: ''
         
        };
      },
      //新增
      addSubmit: function () {
        let that = this;
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            that.loading = true;
            let para = Object.assign({}, this.addForm);
        
            API.add(para).then(function (result) {
              that.loading = false;
              if (result) {
                that.$message.success({showClose: true, message: '新增成功', duration: 2000});
                that.$refs['addForm'].resetFields();
                that.addFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '修改失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });

          }
        });
      },


      //显示编辑界面
      showEditDialog: function (index, row) {

        let params={
          rolename:0,
          fenjiaoyuju:1
        };
        let self= this;
        self.options_fenjiaoyuju = [];
        API.findFen(params).then(function (result) {
          let x = result.data
          if (x) {
            x.forEach(item => {
              self.options_fenjiaoyuju.push({
                value1: item.id,
                label1: item.loginname
              });
            });
          }
        });




        this.editFormVisible = true;
        this.editForm = Object.assign({}, row);
      },
      //编辑
      editSubmit: function () {
        let that = this;
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.loading = true;
            let para = Object.assign({}, this.editForm);
            
            API.update(para.id, para).then(function (result) {
              that.loading = false;
              if (result) {
                that.$message.success({showClose: true, message: '修改成功', duration: 2000});
                that.$refs['editForm'].resetFields();
                that.editFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '修改失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });
          }
        });
      },


      //删除
      delOrgan: function (index, row) {
        let that = this;
        let params = {
          organid:row.id
          
        };
        this.$confirm('确认删除该记录吗?', '提示', {type: 'warning'}).then(() => {
          that.loading = true;
          API.remove(params).then(function (result) {
            that.loading = false;
            if (result) {
              that.$message.success({showClose: true, message: '删除成功', duration: 1500});
              that.search();
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            
          });
        }).catch(() => {
        });
      }
    },
    mounted() {
      this.handleSearch()
    }
  }
</script>

<style scoped>
.addorganbtn{
  margin-top: 10px;
}
</style>


