<template>

  <el-row class="container">
  
    <!--头部-->
    <el-col :span="24" class="topbar-wrap">
      <!--<div class="topbar-logo topbar-btn">
        <a href="/"><img src="../assets/logo.png" style="padding-left:25px;"></a>
      </div>-->
      <div class="topbar-logos" v-show="!collapsed">
        <a href="/"><img src="../assets/images/logo.png"></a>
      </div>
      <div class="topbar-title">
        <span style="font-size: 30px;color: #fff;">无线信号干扰远程监控系统</span>
        <!-- <img src="../assets/images/sysname.png" class=""> -->
      </div>
      <div class="topbar-account topbar-btn">
        <el-dropdown trigger="click">
          <!--<span class="quanping"> <i class="iconfont icon-fullscreen" @click="requestFullScreen"></i></span>-->
          <span class="el-dropdown-link userinfo-inner"><i class="iconfont icon-geren"></i> {{nickname}}  <i
            class="iconfont icon-down"></i></span>
          <el-dropdown-menu slot="dropdown">
            <!--<el-dropdown-item>
              <div @click="jumpTo('/user/profile')"><span style="color: #555;font-size: 14px;">个人信息</span></div>
            </el-dropdown-item>
            <el-dropdown-item>
              <div @click="jumpTo('/user/changepwd')"><span style="color: #555;font-size: 14px;">修改密码</span></div>
            </el-dropdown-item>-->
            <!--<el-dropdown-item divided @click.native="toggle">全屏</el-dropdown-item>-->
            <el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-col>

    <!--中间-->
    <el-col :span="24" class="main">
      <!--左侧导航-->
      <aside :class="{showSidebar:!collapsed}">
        <!--展开折叠开关-->
        <!-- <div class="menu-toggle" @click.prevent="collapse">
          <i class="iconfont icon-menufold" v-show="!collapsed"></i>
          <i class="iconfont icon-menuunfold" v-show="collapsed"></i>
        </div> -->
        <!--导航菜单-->
        <el-menu :default-active="defaultActiveIndex" router :collapse="collapsed" @select="handleSelect" class="routername">
          <template v-for="(item,index) in $router.options.routes" v-if="item.menuShow">
            <el-submenu v-if="!item.leaf" :index="index+''">
              <template slot="title"><i :class="item.iconCls"></i><span  slot="title">{{item.name}}</span></template>
              <el-menu-item v-for="term in item.children" :key="term.path" :index="term.path" v-if="term.menuShow"
                            :class="$route.path==term.path?'is-active':''">
                <i :class="term.iconCls"></i><span slot="title">{{term.name}}</span>
              </el-menu-item>
            </el-submenu>
            <el-menu-item v-else-if="item.leaf&&item.children&&item.children.length" :index="item.children[0].path"
                          :class="$route.path==item.children[0].path?'is-active':''">
              <i :class="item.iconCls"></i><span slot="title">{{item.children[0].name}}</span>
            </el-menu-item>
          </template>
        </el-menu>
      </aside>

      <!--右侧内容区-->
      
          <!-- <section class="content-container"> -->
          <!-- <div class="content-container">
            <div class="grid-content bg-purple-light">
            
              <el-col :span="24" class="content-wrapper">
                <transition name="fade" mode="out-in">
                  <router-view></router-view>
                </transition>
              </el-col>
               
            </div>
          </div> -->
          <!-- </section> -->

          <div class="content-container">
            <router-view></router-view>
          </div>
      

    </el-col>

  </el-row>
  
</template>

<script>
  import {bus} from '../bus.js'
  import API from '../api/api_user';
  import { powerRouter } from '../router/index';
  import { powerRouterObj } from '../router/index';
  import organ from "../api/api_organ";
  

  export default {
    name: 'home',
    // created(){
    //   bus.$on('setNickName', (text) => {
    //     this.nickname = text;
    //   })

    //   bus.$on('goto', (url) => {
    //     if (url === "/login") {
    //       localStorage.removeItem('access-user');
    //     }
    //     this.$router.push(url);
    //   })
    // },
    data () {
      return {
        defaultActiveIndex: "0",
        nickname: '',
        collapsed: false,
        //fullscreen: false,
      }
    },
    methods: {
      handleSelect(index){
        this.defaultActiveIndex = index;
      },
      //折叠导航栏
      collapse: function () {
        this.collapsed = !this.collapsed;
      },
      jumpTo(url){
        this.defaultActiveIndex = url;
        this.$router.push(url); //用go刷新
      },
      logout(){
        let that = this;
        this.$confirm('确认退出吗?', '提示', {
          confirmButtonClass: 'el-button--warning'
        }).then(() => {
            //确认
            that.loading = true;
            localStorage.removeItem('access-user');
            that.$router.go('/login'); //用go刷新
           
        }).catch(() => {});
      },



      // toggle () {
      //   this.$refs['fullscreen'].toggle() // recommended
      //   // this.fullscreen = !this.fullscreen // deprecated
      // },
      // fullscreenChange (fullscreen) {
      //   this.fullscreen = fullscreen
      // }
      
    },
    // beforeCreate() {

    //   // console.log('created addRoutes:' +this.$router + ' ' + powerRouter);
    //   // console.log(this.$router)
    //   // console.log(powerRouter)

    //   let user = localStorage.getItem('access-user');
    //   if (user) {
    //     user = JSON.parse(user);
    //     this.nickname = user.data.nickname; 
    //   }

    //   //console.log("当前角色号：",user.data.rolename)
    //   if(user.data.rolename==0){
    //     this.$router.options.routes.push(powerRouterObj)
    //     this.$router.addRoutes(powerRouter);
    //   }

    // }

    //beforeCreate() {
    created() {
      
      let user = localStorage.getItem('access-user');
      if (user) {
        user = JSON.parse(user);
        this.nickname = user.data.nickname; 
      }

      // this.$router.addRoutes(powerRouter);
      // this.$router.options.routes.push(powerRouterObj)
      // console.log("router：")
      // console.log(this.$router)

      //console.log("当前角色号：",user.data.rolename)
      if(user.data.rolename==0&&user.data.fenjiaoyuju==0){

        this.$router.addRoutes(powerRouter);
        this.$router.options.routes.push(powerRouterObj)
        //this.$router.addRoutes(this.$router.options.routes);
        //console.log(this.$router);
       
        //localStorage.getItem('menu')

        //存菜单
        //sessionStorage.setItem('routes',JSON.stringify(powerRouter));

        //let newroute = this.$router.options.routes.concat(powerRouter)
        //this.$router.addRoutes(this.$router.options.routes);
      }
      
    },





 
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  /* html {
    
     background: #040045;
    
  } */

</style>


<style scoped lang="scss">
  .container {
    position: absolute;
    top: 0px;
    bottom: 0px;
    width: 100%;

    .topbar-wrap {
      height: 80px;
      line-height: 80px;
      /* 头部颜色 */
      // background: #7cc8f7;
      background: #092675;
      /*background: #00142a;*/
      padding: 0px;

      .topbar-btn {
        color: #fff;
      }
      /*.topbar-btn:hover {*/
      /*background-color: #4A5064;*/
      /*}*/
      .topbar-logo {
        float: left;
        width: 59px;
        line-height: 26px;
      }
      .topbar-logos {
        float: left;
        width: 179px;
        line-height: 26px;
      }
      .topbar-logo img, .topbar-logos img {
        height: 50px;
        margin-top: 15px;
        margin-left: 10px;
      }
      .topbar-title {
        float: left;
        text-align: left;
        width: 500px;
        padding-left: 10px;
        border-left: 1px solid #000;
        
        
      }
      .topbar-account {
        float: right;
        padding-right: 12px;
      }
      .userinfo-inner {
        cursor: pointer;
        color: #fff;
        padding-left: 10px;
      }
    }
    .main {
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      position: absolute;
      top: 50px;
      bottom: 0px;
      overflow: hidden;
    }

    aside {
      margin-top: 30px;
      min-width: 50px;
      /*侧边栏底部*/
      // background: #a3d8f9;
      background: #092675;
      
      
      &::-webkit-scrollbar {
        display: none;
      }

      &.showSidebar {
        overflow-x: hidden;
        overflow-y: auto;
      }

      .el-menu {
        height: 100%; /*写给不支持calc()的浏览器*/
        height: -moz-calc(100% - 80px);
        height: -webkit-calc(100% - 80px);
        height: calc(100% - 80px);
        border-radius: 0px;
        /*侧边栏颜色*/
        // background-color: #a3d8f9;
        background-color: #092675;
        // background-color: #00142a;
        
        
        border-right: 0px;
      }

      .el-submenu .el-menu-item {
        min-width: 60px;
      }
      .el-menu {
        width: 180px;
      }
      .el-menu--collapse {
        width: 60px;
      }

      .el-menu .el-menu-item, .el-submenu .el-submenu__title {
        height: 46px;
        line-height: 46px;
        color:white;
        font-family: "微软雅黑"
      }

      .el-menu-item:hover, .el-submenu .el-menu-item:hover, .el-submenu__title:hover {
        background-color: #7ed2df;
      }
    }

    .menu-toggle {
      /* 折叠组件 */
      background: #a3d8f9;
      /*background: #00142a;
        background: #4A5064;
      */
      text-align: center;
      color: black;
      height: 26px;
      line-height: 30px;
    }

    .content-container {
       //background: #040045;
       width: 100%;
       height:100%;
      // margin-top: 10px;
      background-image: url(../assets/images/mainbg.png);
      background-size:100% 100%;
      flex: 1;
      overflow-y: auto;
      //box-sizing: border-box;
      // overflow-x: auto;
      // padding: 10px;
      // padding-bottom: 1px;

      // .content-wrapper {
      //    background-color: #040045;
        
      //   background: url(../assets/images/mainbg.png) no-repeat ;
      //   background-size:90% 90%;
      //   // box-sizing: border-box;
      // }

    }

    .quanping{
        color: #fff;
        cursor: pointer;
    }

    .routername{
      background: #4A5064;
    }


  }

</style>



