<template>
  <el-row class="warp">
    <el-col :span="24" class="itemname1">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item onclick="window.history.go(-1)">
          <span class="itemfont">所属单位</span>
        </el-breadcrumb-item>
        <el-breadcrumb-item>
          <span class="itemfont">{{organname}}</span>
        </el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="拼命加载中">
      
      <!--新增按钮-->
      <el-col :span="24" class="toolbar" style="margin-top: 10px;">
        <el-form>
          <el-form-item>
            <el-button type="primary" @click="showAddDialog">新增楼栋</el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!--列表-->
      <el-table :data="data"  highlight-current-row v-loading="loading" style="width: 100%;border-radius:8px;" :header-cell-style="{background:'#98b3d4',color:'black'}">
        <el-table-column type="index" width=""></el-table-column>
        <el-table-column prop="buildingname" label="楼栋名称" width="" >
        </el-table-column>
        <el-table-column prop="organ.organname" label="所属单位" width="" >
        </el-table-column>

        <el-table-column label="操作" width="500">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditDialog(scope.$index,scope.row)">编辑</el-button> 
            <el-button type="danger" size="small" @click="delBuilding(scope.$index,scope.row)">删除</el-button>
            
          </template>
        </el-table-column>
      </el-table>

      <!--编辑界面-->
      <el-dialog title="编辑" :visible.sync ="editFormVisible" :close-on-click-modal="false">
        <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
          <el-form-item label="楼栋名称" prop="buildingname">
            <el-input v-model="editForm.buildingname" auto-complete="off"></el-input>
          </el-form-item>
           
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="editFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="editSubmit">提交</el-button>
        </div>
      </el-dialog>


      <!--新增界面-->
      <el-dialog title="新增楼栋" :visible.sync ="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="100px" :rules="addFormRules" ref="addForm">

          <!--<el-form-item label="所属单位" prop="orgid">
            <el-select v-model="addForm.orgid" placeholder="请选择">
              <el-option
                v-for="item in options_orgname"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select> 
          </el-form-item>-->
          <el-form-item label="所属单位" prop="orgid">
            <el-input  :value="organname" disabled></el-input>
          </el-form-item>

          <el-form-item label="楼栋名称" prop="buildingname">
            <el-input v-model="addForm.buildingname" auto-complete="off"></el-input>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
        </div>
      </el-dialog>

      <!--
      <el-select v-model="value" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>  -->

    </el-col>
  </el-row>
</template>

<script>
  import API from '../../api/api_building';
  import organ from '../../api/api_organ';

  export default {
    data() {
      return {
        //orgid: this.$route.query.id,
        organname:this.$route.query.organname,

        options_orgname:[],


        filters: {
          name: ''
        },
        loading: false,
        data: [],
        // total: 0,
        // page: 1,
        // limit: 10,
        loading: false,


        //编辑相关数据
        editFormVisible: false,//编辑界面是否显示
        editFormRules: {
          
          buildingname: [
            {required: true, message: '请输入楼栋名称', trigger: 'blur'}
          ]
        
          
        },
        editForm: {
          buildingname: ''
          
        },

        //新增相关数据
        addFormVisible: false,//新增界面是否显示
        addLoading: false,
        addFormRules: {
          
          buildingname: [
            {required: true, message: '请输入楼栋名称', trigger: 'blur'}
          ]
          

        },
        addForm: {
          orgid: '',
          buildingname: ''
          
          
        }
      }
    },
    methods: {
      //性别显示转换
      // formatSex: function (row, column) {
      //   return row.sex == 1 ? '男' : row.sex == 0 ? '女' : '未知';
      // },
      // handleCurrentChange(val) {
      //   //this.page = val;
      //   this.search();
      // },
      handleSearch(){
        // this.total = 0;
        // this.page = 1;
        this.search();
      },
      //获取用户列表
      search: function () {
        let that = this;
        let params = {
          orgid: this.$route.query.id
        };

        that.loading = true;
        API.findList(params).then(function (result) {
          that.loading = false;
          if (result && result.data) {
            that.total = result.total;
            that.data = result.data;
          }
        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
        });
      },

      //显示新增弹框
      showAddDialog: function () {
        let params={};
        let self= this;
        self.options_orgname = [];
        organ.findList(params).then(function (result) {
          let x = result.data
          if (x) {
            x.forEach(item => {
              self.options_orgname.push({
                value: item.id,
                label: item.organname
              });
            });
          }
        });

        this.addFormVisible = true;
        this.addForm = {
          orgid: this.$route.query.id,
          buildingname: ''
        };
      },
      //新增
      addSubmit: function () {
        let that = this;
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            that.loading = true;
            let para = Object.assign({}, this.addForm);
        
            API.add(para).then(function (result) {
              that.loading = false;
              if (result) {
                that.$message.success({showClose: true, message: '新增成功', duration: 2000});
                that.$refs['addForm'].resetFields();
                that.addFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '修改失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });

          }
        });
      },


      //显示编辑界面
      showEditDialog: function (index, row) {
        this.editFormVisible = true;
        this.editForm = Object.assign({}, row);
      },
      //编辑
      editSubmit: function () {
        let that = this;
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.loading = true;
            let para = Object.assign({}, this.editForm);
            
            API.update(para.id, para).then(function (result) {
              that.loading = false;
              if (result) {
                that.$message.success({showClose: true, message: '修改成功', duration: 2000});
                that.$refs['editForm'].resetFields();
                that.editFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '修改失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });
          }
        });
      },


      //删除
      delBuilding: function (index, row) {
        let that = this;
        let params = {
          buildingid:row.id
          
        };
        this.$confirm('确认删除该记录吗?', '提示', {type: 'warning'}).then(() => {
          that.loading = true;
          API.remove(params).then(function (result) {
            that.loading = false;
            if (result) {
              that.$message.success({showClose: true, message: '删除成功', duration: 1500});
              that.search();
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            
          });
        }).catch(() => {
        });
      }
    },
    mounted() {
      this.handleSearch()
    }
  }
</script>

<style scoped>

</style>


