<template>
  <el-row class="warp">
      <section class="chart-container">

        <el-row>
          <div class="itemname">
            <el-breadcrumb separator="/">
              <el-breadcrumb-item>
                <b class="itemfont">首页</b>
              </el-breadcrumb-item>
            </el-breadcrumb>
          </div>

          <!--教育局 -->
          <!-- <el-col  v-for="(item,index) in organList" :key="index"> -->
            <div v-for="(item,index) in organList" :key="index" class="listwrap">
              <div @click="showJammerByOrgan(item.value,item.label)" class="jammercard" v-if="rolename==0">
              <!--<el-card :body-style="{ padding: '10px'}" @click.native="showJammerByOrgan(item.value,item.label)" class="jammercard" v-if="rolename==0">-->
                <div class="image">
                  <img src="../../assets/images/xuexiaologo.png" class="xuexiaoimg">
                </div>
                <div class="schoolname">
                  <span>{{item.label}}</span>
                </div>
                
              <!--</el-card>-->
              </div>
            </div>

            
          <!-- </el-col> -->

          <!--单位 -->
          <!--<el-col :span="4">
            <el-card :body-style="{ padding: '10px'}" @click.native="showJammerByOrgan(orgid,orgname)" class="jammercard" v-if="rolename!=0">
              <img src="../../assets/images/school.jpg" class="image">
              <div style="text-align:center;padding: 1px;">
                <span>{{orgname}}</span>
              </div>
            </el-card>
          </el-col>-->

        </el-row>
      </section>

  </el-row>
</template>


<script>
//import echarts from "echarts";
import organ from "../../api/api_organ";
import { powerRouterObj } from '../../router/index';
import { powerRouter } from '../../router/index';


export default {
  data() {
    return {
      organList: [],
      orgid:'',
      orgname:'',
      rolename:''
    };
  },

// created() {
//   const returnValue = 'Are you sure you want to lose unsaved changes?';
//     document.onbeforeunload = function (e) {
//         e = e || document.event;

//         // 兼容IE8和Firefox 4之前的版本
//         if (e) {
//           e.returnValue = '关闭提示';
//         }

//         // Chrome, Safari, Firefox 4+, Opera 12+ , IE 9+
//         return '关闭提示';
//       };
  
// },

  created: function() {
        // //检测本地路由
        // let localRoutes = sessionStorage.getItem('routes');
        // if(localRoutes){
        //     this.$router.addRoutes(JSON.parse(localRoutes));
        // }

    //localStorage.setItem('new',JSON.stringify(powerRouterObj))//保存路由
    //console.log("存储的路由是：",JSON.parse(localStorage.getItem('new')))

    let self = this;
    let user = JSON.parse(window.localStorage.getItem('access-user'))
    self.rolename=user.data.rolename
    //console.log(self.rolename)
    

    let params = {
      organid:user.data.rolename,
      fenjiaoyuju:user.data.fenjiaoyuju,
      userid: user.data.userid

    };
    organ.findList(params).then(function(result) {
      let x = result.data;
      if(user.data.rolename==0){
         x.forEach(item => {
          self.organList.push({
            value: item.id,
            label: item.organname
          });
        });
      }else{
        self.orgid=x.id
        self.orgname=x.organname
        self.showJammerByOrgan(x.id,x.organname)
      } 
    });

    // if(self.rolename!=0){
    //   console.log("id为：",21)
    // }

  

  },



  methods: {
    showJammerByOrgan(x, y) {
      this.$router.push({
        path: "/jammer/infobyorgan",
        query: {
          id: x,
          organname: y
        }
      });
    }
  }
};

</script>

<style>

.itemname{
  margin-top: 30px;
}
.itemfont{
  color: white
}

.guangimg{
  height: 100px;
  width: 200px;
  
}


@media (min-width: 0px) {
    .jammercard {
      width: 50%;
      float: left;
      height: auto;
      /* padding-bottom: 5rem; */
      padding: 3rem;
    }
}

@media (min-width: 800px) {
    .jammercard {
      width: 40%;
      float: left;
      height: auto;
      /* padding-bottom: 5rem; */
      padding: 3rem;
    }
  }
  @media (min-width: 1100px) {
    .jammercard {
      width: 23.3%;
      float: left;
      height: auto;
      /* padding-bottom: 5rem; */
      padding: 3rem;
    }
  }
  @media (min-width: 1500px) {
    .jammercard {
      width: 15%;
      float: left;
      height: auto;
      /* overflow-y: scroll; */
      padding: 3rem;
      /* padding-left: 3rem;
      padding-bottom: 3rem; */
      
    }
  }
  @media (min-width: 1800px) {
    .jammercard {
      width: 12%;
      float: left;
      height: auto;
      /* overflow-y: scroll; */
      padding: 3rem;
      /* padding-left: 3rem;
      padding-bottom: 3rem; */
      
    }
  }

.schoolname{
  margin-top:-80px;
  text-align: center;
  font-family:"微软雅黑";
  color: #ff00de;
  font-weight:600;
}

  /* .listwrap {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    padding-bottom: 10rem; // margin-bottom: 8rem;
  } */





/* .image {
 
  display: block;
} */

/* .clearfix:before,
.clearfix:after {
  display: table;
  content: "";
} */

/* .clearfix:after {
  clear: both;
} */
/* .warp{
  background-color: #9119dd
} */




.chart-container {
  width: 100%;
  /* background-color: #9119dd */
}

.chart-container .el-col {
  padding: 30px 20px;
  cursor: pointer;
}



/*.jammercard:hover{
    background-color:#5f6d6a;
}*/




</style>



