<template>
  <el-row class="warp">
    <!-- <el-col :span="24" class="warp-breadcrum"> -->
    <div class="itemname1">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item onclick="window.history.go(-1)">
          <b class="itemfont">单位</b>
        </el-breadcrumb-item>
        <el-breadcrumb-item>
          <span class="itemfont">{{organname}}</span>
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- </el-col> -->

   <el-col :span="24" class="warp-main">
      <section class="chart-container">
        <el-row>
          <el-col :span="3" v-for="(item,index) in buildingList" :key="index">
            <div @click="showJammer(item.value,item.label)" class="jammerbuilding">
            <!--<el-card :body-style="{ padding: '10px'}" @click.native="showJammer(item.value,item.label)" class="jammercard">-->
              <img src="../../assets/images/lou.png" class="image">
              <!--<div style="padding: 14px;">-->
                <div class="loudong">
                  <span>{{item.label}}</span>
                </div>
              <!--<div style="text-align:center;padding: 1px;">
                <span>{{item.label}}</span>
              </div>-->
            <!--</el-card>-->
            </div>
          </el-col>
        </el-row>
      </section>
    </el-col>

  </el-row>
</template>

<script>

  import building from '../../api/api_building';

  export default {
    data() {
      return {
        organname:this.$route.query.organname,
        buildingList: []
      }
    },
    mounted: function () {
      
      let self = this;
      let params = {
        orgid: this.$route.query.id
      };
      building.findList(params).then(function (result) {
        let x = result.data
        if (x) {
          x.forEach(item => {
            self.buildingList.push({
              value: item.id,
              label: item.buildingname
            });
          });
        }
      });
    },
    methods: {
      showJammer(x,y) {
        this.$router.push({
          path: "/jammer/info",
          query: {
            id: x,
            buildingname:y,
            orgid: this.$route.query.id,
            organname:this.$route.query.organname
          }
          
        });
      }
    }
  };

</script>

<style>
.itemfont{
  color: white
}

.time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both;
  }

  .chart-container {
    width: 100%;
  }

  .chart-container .el-col {
    padding: 30px 20px;
    cursor:pointer;
  }

  .jammercard{

  }

  .loudong{
    /* float:right; */
    /* margin-right:17px; */
    /* margin-right:10%; */
    /* margin-top:-90px; */
    /* margin-top:-50%; */
    text-align:center;
    margin-top: -10%;
    color: white;
    font-size: 19px;
  }

  /*.jammerbuilding:hover{
    /*background-color:#5f6d6a;
    background-color:#757d86;
  }*/

</style>



