<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b>首页</b></el-breadcrumb-item>
        <el-breadcrumb-item>屏蔽器</el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="拼命加载中">
      
      <!--新增按钮-->
      <el-col :span="2" class="toolbar" style="padding-bottom: 0px;">
        <el-form>
          <el-form-item>
            <el-button type="primary" @click="showAddDialog">新增屏蔽器</el-button>
          </el-form-item>
        </el-form>
      </el-col>


      <!--列表-->
      <el-table :data="data" border highlight-current-row v-loading="loading" style="width: 100%;">
        <el-table-column type="index" width=""></el-table-column>
        <el-table-column prop="jammerno" label="屏蔽器编号" width="" sortable>
        </el-table-column>
        <el-table-column prop="jammerip" label="屏蔽器IP" width="" sortable>
        </el-table-column>
        <el-table-column prop="building.organ.organname" label="所属单位" width="" sortable>
        </el-table-column>
        <el-table-column prop="building.buildingname" label="所属楼栋" width="" sortable>
        </el-table-column>
        <el-table-column prop="floorname" label="所属楼层" width="" sortable>
        </el-table-column>
        <el-table-column prop="locationinfo" label="安装位置" width="" sortable>
        </el-table-column>
        <el-table-column label="操作" width="500">
          <template slot-scope="scope">
            <el-button size="small"   @click="showEditDialog(scope.$index,scope.row)">编辑</el-button> 
            <el-button type="danger"  size="small" @click="delJammer(scope.$index,scope.row)">删除</el-button>
            <el-button type="success" size="small" @click="start(scope.$index,scope.row)">开启</el-button>
            <el-button type="warning" size="small" @click="close(scope.$index,scope.row)">关闭</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--编辑界面-->
      <el-dialog title="编辑" :visible.sync ="editFormVisible" :close-on-click-modal="false">
        <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
          <el-form-item label="屏蔽器编号" prop="jammerno">
            <el-input v-model="editForm.jammerno" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="屏蔽器IP" prop="jammerip">
            <el-input v-model="editForm.jammerip" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="所属单位" prop="orgname">
            <el-input v-model="editForm.orgname" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="所属楼栋" prop="buildingname">
            <el-input v-model="editForm.buildingname" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="所属楼层" prop="floorname">
            <el-input v-model="editForm.floorname" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="安装位置" prop="locationinfo">
            <el-input v-model="editForm.locationinfo" auto-complete="off"></el-input>
          </el-form-item>
          
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="editFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="editSubmit">提交</el-button>
        </div>
      </el-dialog>


      <!--新增界面-->
      <el-dialog title="新增" :visible.sync ="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="100px" :rules="addFormRules" ref="addForm">

          <el-form-item label="屏蔽器编号" prop="jammerno">
            <el-input v-model="addForm.jammerno" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="屏蔽器IP" prop="jammerip">
            <el-input v-model="addForm.jammerip" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="所属单位" prop="orgid">
            <!-- <el-input v-model="addForm.orgname" auto-complete="off"></el-input> -->
            <el-select v-model="addForm.orgid" placeholder="请选择">
              <el-option
                v-for="item in options_orgname"
                :key="item.value"
                :label="item.label1"
                :value="item.value1">
              </el-option>
            </el-select> 
          </el-form-item>

          <el-form-item label="所属楼栋" prop="buildingid">
            <!-- <el-input v-model="addForm.buildingname"></el-input> -->
            <el-select v-model="addForm.buildingid" placeholder="请选择">
              <el-option
                v-for="item in options_building"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="所属楼层" prop="floorname">
            <!-- <el-input v-model="addForm.floorname"></el-input> -->
            <el-select v-model="addForm.floorname" placeholder="请选择">
              <el-option
                v-for="item in options_floor"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="安装位置" prop="locationinfo">
            <el-input v-model="addForm.locationinfo"></el-input>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
        </div>
      </el-dialog>

      <!--
      <el-select v-model="value" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>  -->

    </el-col>
  </el-row>
</template>

<script>
  import API from '../../api/api_jammer';
  import organ from '../../api/api_organ';
  //import floor from '../../api/api_floor';
  import building from '../../api/api_building';

  export default {
    data() {
      return {
        options_orgname:[],
        options_building:[],
        options_floor:[],
        
        filters: {
          name: ''
        },
        loading: false,
        data: [],
        // total: 0,
        // page: 1,
        // limit: 10,
        loading: false,


        //编辑相关数据
        editFormVisible: false,//编辑界面是否显示
        editFormRules: {
          jammerno: [
            {required: true, message: '请输入屏蔽器编号', trigger: 'blur'}
          ],
          jammerip: [
            {required: true, message: '请输入屏蔽器IP', trigger: 'blur'}
          ],
          orgname: [
            {required: true, message: '请输入所属单位', trigger: 'blur'}
          ],
          buildingname: [
            {required: true, message: '请输入所属楼栋', trigger: 'blur'}
          ],
          floorname: [
            {required: true, message: '请输入所属楼层', trigger: 'blur'}
          ],
          locationinfo: [
            {required: true, message: '请输入安装位置', trigger: 'blur'}
          ]
          
        },
        editForm: {
          jammerno: '',
          jammerip: '',
          orgname: '',
          buildingname: '',
          floorname: '',
          locationinfo: ''
        },

        //新增相关数据
        addFormVisible: false,//新增界面是否显示
        addLoading: false,
        addFormRules: {

          jammerno: [
            {required: true, message: '请输入屏蔽器编号', trigger: 'blur'}
          ],
          jammerip: [
            {required: true, message: '请输入屏蔽器IP', trigger: 'blur'}
          ],
          
          floorname: [
            {required: true, message: '请输入所属楼层', trigger: 'blur'}
          ],
          locationinfo: [
            {required: true, message: '请输入安装位置', trigger: 'blur'}
          ]

        },
        addForm: {
          jammerno: '',
          jammerip: '',
          orgid: '',
          buildingid: '',
          floorname: '',
          locationinfo: ''
        }
      }
    },
    // mounted() {
    //   let params = {};    
    //   organ.findList(params).then(function (result) {
    //     let x = result.data
    //     if (x) {
    //       x.forEach(item => {
    //         this.options.push({
    //           value: item.id,
    //           label: item.organname
    //         });
    //       });
    //     }
    //     });
    // },
    methods: {
      //性别显示转换
      // formatSex: function (row, column) {
      //   return row.sex == 1 ? '男' : row.sex == 0 ? '女' : '未知';
      // },
      // handleCurrentChange(val) {
      //   //this.page = val;
      //   this.search();
      // },
      handleSearch(){
        // this.total = 0;
        // this.page = 1;
        this.search();
      },

      //获取屏蔽器列表
      search: function () {
        let that = this;
        let params = {
          
        };

        that.loading = true;
        API.findList(params).then(function (result) {
          that.loading = false;
          if (result && result.data) {
            //that.total = result.total;
            that.data = result.data;
          }
        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
        });
      },

      //显示新增界面
      showAddDialog: function () {
        
        let params1={};
        let self= this;
        self.options_orgname = [];
        organ.findList(params1).then(function (result) {
          let x = result.data
          if (x) {
            x.forEach(item => {
              self.options_orgname.push({
                value1: item.id,
                label1: item.organname
              });
            });
          }
        });


    
        let params2={};
        self.options_building = [];
        building.findList(params2).then(function (result) {
          let x = result.data
          if (x) {
            x.forEach(item => {
              self.options_building.push({
                value: item.id,
                label: item.buildingname
              });
            });
          }
        });

        let params3={};
        self.options_floor = [];     
        floor.findList(params3).then(function (result) {
          let x = result.data
          if (x) {
            x.forEach(item => {
              self.options_floor.push({
                value: item.id,
                label: item.floorname
              });
            });
          }
        });
        
        this.addFormVisible = true;
        this.addForm = {
          jammerno: '',
          jammerip: '',
          orgid: '',
          buildingid: '',
          floorname: '',
          locationinfo: ''
        };
      },
      //新增
      addSubmit: function () {
        let that = this;
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            that.loading = true;
            let para = Object.assign({}, this.addForm);
        
            API.add(para).then(function (result) {
              that.loading = false;
              if (result) {
                that.$message.success({showClose: true, message: '新增成功', duration: 2000});
                that.$refs['addForm'].resetFields();
                that.addFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '修改失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });

          }
        });
      },


      


      //显示编辑界面
      showEditDialog: function (index, row) {
        this.editFormVisible = true;
        this.editForm = Object.assign({}, row);
      },
      //编辑
      editSubmit: function () {
        let that = this;
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.loading = true;
            let para = Object.assign({}, this.editForm);
            
            API.update(para.id, para).then(function (result) {
              that.loading = false;
              if (result) {
                that.$message.success({showClose: true, message: '修改成功', duration: 2000});
                that.$refs['editForm'].resetFields();
                that.editFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '修改失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });
          }
        });
      },


      //开启
      start: function (index, row) {
        let that = this;
        let params = {
          jammerid : row.id,
          status : 1
          
        };
        this.$confirm('确认开启屏蔽器吗?', '提示', {type: 'warning'}).then(() => {
          that.loading = true;
          API.start(params).then(function (result) {
            that.loading = false;
            if (result) {
              that.$message.success({showClose: true, message: '开启成功', duration: 1500});
              
              that.search();
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            
          });
        }).catch(() => {
        });
      },

      //关闭
      close: function (index, row) {
        let that = this;
        let params = {
          jammerid : row.id,
          status : 0
          
        };
        this.$confirm('确认关闭屏蔽器吗?', '提示', {type: 'warning'}).then(() => {
          that.loading = true;
          API.start(params).then(function (result) {
            that.loading = false;
            if (result) {
              that.$message.success({showClose: true, message: '关闭成功', duration: 1500});
              
              that.search();
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            
          });
        }).catch(() => {
        });
      },


      //删除
      delJammer: function (index, row) {
        let that = this;
        let params = {
          id:row.id
          
        };
        this.$confirm('确认删除该记录吗?', '提示', {type: 'warning'}).then(() => {
          that.loading = true;
          API.remove(params).then(function (result) {
            that.loading = false;
            if (result) {
              that.$message.success({showClose: true, message: '删除成功', duration: 1500});
              
              that.search();
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            
          });
        }).catch(() => {
        });
      }
    },
    mounted() {
      this.handleSearch()
    }
  }
</script>

<style scoped>

</style>


