import * as API from './'

export default {


  //查询获取alarm列表
  findList: params => {
    return API.POST('/jammer/alarmlist', params)
  },

  findTime: params => {
    return API.POST('/jammer/alarmtime', params)
  },

  findStatus: params => {
    return API.POST('/jammer/alarmstatus', params)
  }

  


}



