<template>
  <el-row class="warp">
    <!-- <el-col :span="24" class="warp-breadcrum"> -->
    <div class="itemname1">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item >
          <b class="itemfont">单位</b>
        </el-breadcrumb-item>
        <el-breadcrumb-item onclick="window.history.go(-1)">
          <span class="itemfont">{{organname}}</span>
        </el-breadcrumb-item>
         <el-breadcrumb-item>
          <span class="itemfont">{{buildingname}}</span>
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- </el-col> -->


    <!--新增按钮-->
      <el-col :span="24" class="addbtn" style="margin-top: 10px;">
        <el-form>
          <el-form-item>
            <el-button type="primary" @click="showAddDialog">新增屏蔽器</el-button>
          </el-form-item>
        </el-form>
      </el-col>

      

      <el-col :span="24" class="warp-main">
          <section class="chart-container">
              <el-row>
                <el-col :span="2"  v-for="(item,index) in jammerList" :key="index">
                  <el-card :body-style="{ padding: '0px'}" class="jammerfont" @click.native="showJammerDetail(item.value)">
                    <div style="text-align:center;padding: 1px;" >
                      <span>编号：{{item.label}}</span>
                    </div>
                    <!--<img src="../../assets/images/xinhao1.jpg" class="image">-->
                    <img src="../../assets/images/干扰关闭.gif" class="image">
                    <!--<div style="padding: 14px;">-->
                    <div style="text-align:center;padding: 1px;" >
                      <span>位置：{{item.locationinfo}}</span>
                    </div>
                  </el-card>
                </el-col>
              </el-row>
          </section>
      </el-col>


      <!--新增界面-->
      <el-dialog title="新增" :visible.sync ="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="100px" :rules="addFormRules" ref="addForm">

          <el-form-item label="屏蔽器编号" prop="jammerno">
            <el-input v-model="addForm.jammerno" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="mac地址" prop="mac">
            <el-input v-model="addForm.mac" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="所属单位" prop="orgid">
            <el-input  :value="organname" disabled></el-input>
          </el-form-item>

           <!--<el-form-item label="所属单位" prop="orgid">
             <el-input v-model="addForm.orgname" auto-complete="off"></el-input>
            <el-select v-model="addForm.orgid" placeholder="请选择">
              <el-option 
                :value="orgid">
              </el-option>
            </el-select> 
          </el-form-item>-->

          <el-form-item label="所属楼栋" prop="buildingid">
            <el-input :value="buildingname" disabled></el-input>
          </el-form-item>

          <!--<el-form-item label="所属楼栋" prop="buildingid">
            <el-input v-model="addForm.buildingname"></el-input> 
            <el-select v-model="addForm.buildingid" placeholder="请选择">
              <el-option :value="buildingname">
              </el-option>
            </el-select>
          </el-form-item>-->

          <!--<el-form-item label="所属楼层" prop="floorname">
            <el-input v-model="addForm.floorname" auto-complete="off"></el-input>
          </el-form-item>-->

          <el-form-item label="安装位置" prop="locationinfo">
            <el-input v-model="addForm.locationinfo" auto-complete="off"></el-input>
          </el-form-item>


          <el-form-item label="屏蔽器型号" prop="jammerversion" >
            <el-select v-model="addForm.jammerversion" placeholder="请选择">
              <el-option
                v-for="item in options_version"
                :key="item.value"
                :value="item.label1">
              </el-option>
            </el-select> 
          </el-form-item>

           <!--<el-form-item label="是否调节" prop="jammerisadjust" >
            <el-select v-model="addForm.jammerisadjust" placeholder="请选择">
              <el-option label="是" value="1"></el-option>
              <el-option label="否" value="0"></el-option>
            </el-select> 
          </el-form-item>-->

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
        </div>
      </el-dialog>

  </el-row>
</template>

<script>

  import building from '../../api/api_building';
  import API from '../../api/api_jammer';
  import VersionAPI from '../../api/api_version';

  export default {
    data() {

      let jammerip = (rule, value, callback) => {
         if (!value) return callback(new Error('屏蔽器IP不能为空'))
        // if (value.length > 50) return callback(new Error('序列号最多50个字符'))
        this.checkJammerIP(value, callback)
        // 在此处将callback传到验证方法中
      }





      return {

        //orgid: this.$route.query.orgid,
        buildingname:this.$route.query.buildingname,
        organname:this.$route.query.organname,
        //buildingList: [],
        jammerList: [],
        data: [],

        options_version:[],

        // filters: {
        //   name: ''
        // },
        // loading: false,


        //新增相关数据
        addFormVisible: false,//新增界面是否显示
        addLoading: false,
        addFormRules: {

          jammerno: [
            {required: true, message: '请输入屏蔽器编号', trigger: 'blur'}
          ],
          mac: [
            {required: true, message: '请输入屏蔽器mac地址', trigger: 'blur'}
          ],
          // jammerip: [
          //   {validator: jammerip, trigger: 'blur'}
          // ],
          
          locationinfo: [
            {required: true, message: '请输入安装位置', trigger: 'blur'}
          ]

        },
        addForm: {
          jammerno: '',
          mac: '',
          orgid: '',
          buildingid: '',
          
          locationinfo: '',
          jammerversion: '',
          jammerisadjust: ''
        }
      }
    },
    mounted: function () {
      
      this.search()
    },
    methods: {

    showJammerDetail(x) {
      this.$router.push({
        path: "/jammer/detail",
        query: {
            id: x,
            organname:this.$route.query.organname
           
          }
      });
    },

    search: function () {
      
      let self = this;
      let params = {
        orgid: this.$route.query.orgid,
        buildingid: this.$route.query.id
        
      };

      API.findList(params).then(function (result) {
        self.jammerList=[];
        let x = result.data
        if (x) {
          x.forEach(item => {
            self.jammerList.push({
              value: item.id,
              label: item.jammerno,
              jamip: item.jammerip,
              locationinfo:item.locationinfo
            });
          });
        }
      });
    },
    

      //显示添加界面
      showAddDialog: function () {

        let self= this;
        self.options_version = [];
        let params1={
          
        };
        VersionAPI.findList(params1).then(function (result) {
          let x = result.data
          if (x) {
            x.forEach(item => {
              self.options_version.push({
                value1: item.id,
                label1: item.version
              });
            });
          }

        });
        
        this.addFormVisible = true;
        this.addForm = {
          jammerno: '',
          jammerip: '',
          orgid: this.$route.query.orgid,
          buildingid: this.$route.query.id,
          locationinfo: '',
          jammerversion: '',
          jammerisadjust: ''

        };
      },
      //新增
      addSubmit: function () {
        let that = this;
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            that.loading = true;
            let para = Object.assign({}, this.addForm);

            API.add(para).then(function (result) {
              that.loading = false;
              if (result) {
                that.$message.success({showClose: true, message: '新增成功', duration: 2000});
                that.$refs['addForm'].resetFields();
                that.addFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '修改失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: 'ip可能被占用', duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });

          }
        });
      },



      checkJammerIP(val, callback) {  // 此方法不能用async,原因未知
        
        this.jammerList.forEach(item => {
            //console.log(item.jamip)
            if(val==item.jamip){
              console.log("这个IP被占用，换个IP吧")
              callback(new Error('这个IP被占用，换个IP吧'))
            }
    
          });
        
         callback()
      }







    }
  };

</script>

<style>
.itemfont{
  color: white
}

.time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both;
  }

  .chart-container {
    width: 100%;
  }

  .chart-container .el-col {
    padding: 30px 20px;
    cursor:pointer;
  }

  .jammerfont{
     font-size: 14px;
     
  }

</style>



