                      <el-button type="success" size="small" @click="savePower4()">保存到A4</el-button>
                    </div>
                  </el-col>
                </el-row> 
                <el-row>
                  <el-col :span="2" class="itemfont">150M</el-col>
                  <el-col :span="14">
                      <div class="grid-slider-left">
                          <el-slider v-model="ch5" show-input :max="31"></el-slider>
                      </div>
                  </el-col>
                  <el-col :span="2">
                    <div class="btn-save">
                      <el-button type="success" size="small" @click="savePower5()">保存到A5</el-button>
                    </div>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="2" class="itemfont">380M</el-col>
                  <el-col :span="14">
