                  </el-col>
                  <el-col :span="2">
                    <div class="btn-save">
                      <el-button type="success" size="small" @click="savePower6()">保存到A6</el-button>
                    </div>
                  </el-col>
                </el-row> 
              </div>


             <div class="rightdevice"> 
                <el-row>
                  <el-col :span="2" class="itemfont">WCDMA</el-col>
                  <el-col :span="14">
                      <div class="grid-slider-right">
                          <el-slider  v-model="ch7" show-input :max="31"></el-slider>
                      </div>
                  </el-col>
                  <el-col :span="2">
                    <div class="btn-save">
                      <el-button type="success" size="small" @click="savePower7()">保存到B1</el-button>
                    </div>
                  </el-col>
                </el-row>

                <el-row>
                  <el-col :span="2" class="itemfont">450M</el-col>
                  <el-col :span="14">
                      <div class="grid-slider-right">
                          <el-slider v-model="ch8" show-input :max="31"></el-slider>
                      </div>
                  </el-col>
                  <el-col :span="2">
                    <div class="btn-save">
                      <el-button type="success" size="small" @click="savePower8()">保存到B2</el-button>
                    </div>
                  </el-col>
                </el-row> 
                <el-row>
                  <el-col :span="2" class="itemfont">2.4G</el-col>
                  <el-col :span="14">
                      <div class="grid-slider-right">
