                      </div>
                  </el-col>
                  <el-col :span="2">
                    <div class="btn-save">
                      <el-button type="success" size="small" @click="savePower10()">保存到B4</el-button>
                    </div>
                  </el-col>
                </el-row> 
                <el-row>
                  <el-col :span="2" class="itemfont">2.5G</el-col>
                  <el-col :span="14">
                      <div class="grid-slider-right">
                          <el-slider v-model="ch11" show-input :max="31"></el-slider>
                      </div>
                  </el-col>
                  <el-col :span="2">
                    <div class="btn-save">
                      <el-button type="success" size="small" @click="savePower11()">保存到B5</el-button>
                    </div>
                  </el-col>
