                </el-row> 
              </div> 
</div> 
          </section>
    </el-col>

    <!--编辑界面-->
      <el-dialog title="编辑" :visible.sync ="editFormVisible" :close-on-click-modal="false" width="25%">
        <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm" >
          <el-form-item label="屏蔽器编号" prop="jammerno">
            <el-input v-model="editForm.jammerno" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="屏蔽器MAC" prop="mac">
            <el-input v-model="editForm.mac" auto-complete="off" ></el-input>
          </el-form-item>
          <el-form-item label="所属单位" prop="orgid">
            <el-input v-model="editForm.building.organ.organname" auto-complete="off" disabled></el-input>
          </el-form-item>
          <el-form-item label="所属楼栋" prop="buildingid">
            <el-input v-model="editForm.building.buildingname" auto-complete="off" disabled></el-input>
          </el-form-item>
          <el-form-item label="安装位置" prop="locationinfo">
            <el-input v-model="editForm.locationinfo" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="是否调节" prop="jammerisadjust" >
            <el-select v-model="editForm.jammerisadjust" placeholder="请选择">
              <el-option label="是" value="1"></el-option>
              <el-option label="否" value="0"></el-option>
            </el-select> 
          </el-form-item>
          
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="editFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="editSubmit">提交</el-button>
        </div>
      </el-dialog>

   

  </el-row>
</template>

<script>
//import building from '../../api/api_building';
import API from "../../api/api_jammer";
import POWER from "../../api/api_power";

export default {
  data() {
    return {
      ch1: '',
      ch2: '',
      ch3: '',
      ch4: '',
      ch5: '',
      ch6: '',

      ch7: '',
      ch8: '',
      ch9: '',
      ch10: '',
      ch11: '',
      ch12: '',
      isstart: '',
      isequal: '',
      isadjust: '',
      //orgid: this.$route.query.orgid,
      //buildingname:this.$route.query.buildingname,
      orgname: this.$route.query.organname,
      //buildingList: [],
      jammerList: {
          'building':{
              buildingname:''
          }
      },

      data: [],

        //编辑相关数据
        editFormVisible: false,//编辑界面是否显示
        editFormRules: {
          jammerno: [
            {required: true, message: '请输入屏蔽器编号', trigger: 'blur'}
          ],
          mac: [
            {required: true, message: '请输入屏蔽器MAC', trigger: 'blur'}
          ],
          
          locationinfo: [
            {required: true, message: '请输入安装位置', trigger: 'blur'}
          ]
          
        },
        editForm:{
          'building':{
              buildingname:'',
              'organ':{
                organname:''
              }
          }
      },
       
    };
  },
  created: function() {
    this.search();
  },
  methods: {
    search: function() {
      let self = this;
      let params = {
        jammerid: this.$route.query.id
        //buildingid: this.$route.query.id
      };

      API.listByJammerId(params).then(function(result) {
     
        let x = result.data;
        if (x) {
        //   x.forEach(item => {
        //     self.jammerList.push({
        //       value: item.id,
        //       jammerno: item.jammerno,
        //       jammerip: item.jammerip,
        //       buildingname: item.building.buildingname,
        //       locationinfo: item.locationinfo
        //     });
        //   }
          self.jammerList=x
          self.editForm=x;
          self.ch1=x.channela;
          self.ch2=x.channelb;
          self.ch3=x.channelc;
          self.ch4=x.channeld;
          self.ch5=x.channele;
          self.ch6=x.channelf;

          self.ch7=x.channelg;
          self.ch8=x.channelh;
          self.ch9=x.channeli;
          self.ch10=x.channelj;
          self.ch11=x.channelk;
          self.ch12=x.channell;
          self.isstart=x.isstart;
          self.isequal=x.isequal
          self.isadjust=x.jammerisadjust

          //console.log(self.jammerList.jammerno);
        
        }
      });
    },


    //显示编辑界面
    showEditDialog: function () {
        this.editFormVisible = true;
        // this.editForm = Object.assign({}, );
      },
      //编辑
      editSubmit: function () {
        let that = this;
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.loading = true;
            let para = Object.assign({}, this.editForm);
            
            API.update(para.id, para).then(function (result) {
              that.loading = false;
              if (result) {
                that.$message.success({showClose: true, message: '修改成功', duration: 2000});
                that.$refs['editForm'].resetFields();
                that.editFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '修改失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });
          }
        });
      },

    //删除
    toDelete: function () {
        let that = this;
        let params = {
          id:this.$route.query.id
          
        };
        this.$confirm('确认删除该记录吗?', '提示', {type: 'warning'}).then(() => {
          window.history.back();
          that.loading = true;
          API.remove(params).then(function (result) {
            that.loading = false;
            if (result) {
              that.$message.success({showClose: true, message: '删除成功', duration: 1500});
            //   this.$router.push({
            //         path: "/jammer/infobyorgan"
            //       });
              //that.search();
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            
          });
        }).catch(() => {
        });
      },


      //单个开启
      start: function () {
        let that = this;
        let params = {
          jammerid : this.$route.query.id,
          status : 1
        };
        this.$confirm('确认开启屏蔽器吗?', '提示', {type: 'warning'}).then(() => {
          that.loading = true;
          API.singlestart(params).then(function (result) {
            that.loading = false;
            if (result) {
              //that.$message.success({showClose: true, message: '开启成功', duration: 1500});
              
              that.search();
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
          });
        
        //that.B()

        //console.log("当前是否相等",that.isequal)
        if(that.isequal!=12){
          setTimeout(() => {
            this.start2();
            //console.log('定时开启')
        }, 2000)
        }

        }

        
        
        ).catch(() => {
        });

        // setInterval(() => { 
        //     this.B();
        //     //console.log('定时开启')
        // }, 5000)

        

      },



      //单个开启
      start2: function () {
        let that = this;
        let params = {
          jammerid : this.$route.query.id,
          status : 1
        };
        
          that.loading = true;
          API.singlestart(params).then(function (result) {
            that.loading = false;
            if (result) {
              //that.$message.success({showClose: true, message: '开启成功', duration: 1500});
              that.search();
            }
          })
     
      },

      //单个关闭
      close: function () {
        let that = this;
        let params = {
          jammerid : this.$route.query.id,
          status : 0
        };
        this.$confirm('确认关闭屏蔽器吗?', '提示', {type: 'warning'}).then(() => {
          that.loading = true;
          API.singlestart(params).then(function (result) {
            that.loading = false;
            if (result) {
              //that.$message.success({showClose: true, message: '关闭成功', duration: 1500});
              
              that.search();
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            
          });

      //有问题的
      // if(that.isequal!=12){
      //     setTimeout(() => {
      //         console.log(2222222)

      //           this.close2();
      //           //console.log('定时单个关闭')
      //         }, 1500)
      // }


          // if(that.isequal!=12){
          //     setTimeout(() => {
          //       this.close2();
          //       //console.log('定时开启')
          //   }, 3000)
          //   }
          
            setTimeout(() => {
              console.log("定时单个关闭")

              if(that.isequal!=12){

                this.close2();
                //this.close2();


              }

                //console.log('定时单个关闭')
              }, 2000)
          


        }).catch(() => {
        });

       


      },


      //单个关闭
      close2: function () {
        let that = this;
        let params = {
          jammerid : this.$route.query.id,
          status : 0
        };

          that.loading = true;
          API.singlestart(params).then(function (result) {
            that.loading = false;
            if (result) {
              that.search();
            }
          })

      },





      //保存到设备A1
      savePower1: function () {
            let that = this;
            let para = {
              id: this.$route.query.id,
              channel_num: 0,
              channel_value: that.ch1
            };
            POWER.add(para).then(function (result) {
                that.$message.success({showClose: true, message: '新增成功', duration: 2000});
              
            }, function (err) {
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});

            }).catch(function (error) {
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });
      },


      //保存到设备A2
      savePower2: function () {
            let that = this;
            let para = {
              id: this.$route.query.id,
              channel_num: 1,
              channel_value: that.ch2
            };
            POWER.add(para).then(function (result) {
                that.$message.success({showClose: true, message: '新增成功', duration: 2000});
              
            }, function (err) {
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});

            }).catch(function (error) {
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });
      },


      //保存到设备A3
      savePower3: function () {
            let that = this;
            let para = {
              id: this.$route.query.id,
              channel_num: 2,
              channel_value: that.ch3
            };
            POWER.add(para).then(function (result) {
                that.$message.success({showClose: true, message: '新增成功', duration: 2000});
              
            }, function (err) {
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});

            }).catch(function (error) {
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });
      },


      //保存到设备A4
      savePower4: function () {
            let that = this;
            let para = {
              id: this.$route.query.id,
              channel_num: 3,
              channel_value: that.ch4
            };
            POWER.add(para).then(function (result) {
