export function setCookie(name, value, iDay) {
  let exDate = new Date();

  exDate.setDate(exDate.getDate() + iDay);　
  document.cookie = name + '=' + value + '; expires = ' + exDate
}

/**
 * 读取cookie
 * @description < 点击保存密码时，读取cookie >
 */
export function getCookie(name) {
  // console.log(name)
  var arr = document.cookie.split(';'); //将cookie切割成数组
  for (var i = 0; i < arr.length; i++)　　 {　　　
    var arr2 = arr[i].split('='); //数组元素以等号切割
    let keyname = arr2[0].replace(/(^\s*)|(\s*$)/g, "");
    if (keyname == name)　　　　 {　　　　　
      return arr2[1]; //获取cookie名对应的cookie值　　
    }　　
  }　　
  return ''; //如果没有获取到值，返回空字符串

}

/**
 * 删除cookie
 * @description < 未点击保存密码时，删除cookie >
 */
export function delCookie(name) {
  setCookie(name, 1, -1)
}



