/**
 * 
 * 屏蔽器相关api
 */
import * as API from './'

export default {

  //查询获取book列表(通过page分页)
  findList: params => {
    return API.POST('/jammer/list',params)
  },

  findListByOrgan: params => {
    return API.POST('/jammer/listbyorgan',params)
  },

  listByJammerId: params => {
    return API.POST('/jammer/listbyjammerid',params)
  },

  //查询获取一条book信息
//   findById: id => {
//     return API.GET(`/api/v1/books/${id}`)
//   },

  add: params => {
    return API.POST(`/jammer/add`, params)
  },
  update: (id, params) => {
    return API.POST(`/jammer/update`, params)
  },

  //单个删除jammer
  remove: params => {
    return API.POST(`/jammer/del`,params)
  },

  //一键开启
  start: params => {
    return API.POST(`/jammer/update_socket_status`,params)
  },

  //一键关闭
  close: params => {
    return API.POST(`/jammer/update_socket_status`,params)
  },

  //单个开启
  singlestart: params => {
    return API.POST(`/jammer/single_socket_status`,params)
  },



  //批量删除，传ids数组
//   removeBatch: (ids) => {
//     return API.DELETE(`/api/v1/books/batch/${ids}`)
//   }

}



