<script>
//import building from '../../api/api_building';
import API from "../../api/api_jammer";
import building from '../../api/api_building';


export default {
  data() {
    return {
      //orgid: this.$route.query.orgid,
      //buildingname:this.$route.query.buildingname,
      //isstart: "",

      organname: this.$route.query.organname,
      //buildingList: [],
      jammerList: [],

      jammerListByOrg: [],   //一键开启push的数组

      jammerListByOrg3: [],  //一键关闭push的数组
      name:'',
      fullscreen: false,

      filters: {
        buildingid: ''
      },

      hangshu:"4",

      options_build:[],
      
      data: []

      
    };
  },

  created:function(){
    this.search();
  },

  mounted: function() {
    //this.search();
    //定时70秒（70 000）
    this.name = setInterval(() => {
            
            //console.log('定时开启')
            this.search();
        }, 10000)
        
  },

  destroyed () {
    clearInterval(this.name)
  },
  methods: {
    search: function() {
      let self = this;
      let params = {
        orgid: this.$route.query.id,
        buildingid: this.filters.buildingid
        //buildingid: this.$route.query.id
      };
      API.findListByOrgan(params).then(function(result) {
        self.jammerList = [];
        let x = result.data;
        if (x) {
          x.forEach(item => {
            self.jammerList.push({
              value: item.id,
              label: item.jammerno,
              ip: item.jammerip,
              mac: item.mac,
              building:item.building.buildingname,
              weizhi: item.locationinfo,
              isstart: item.isstart,
              //isequal: item.isequal

            });
          });
        }
      });



      let params2 = {
        orgid: this.$route.query.id
      };
      self.options_build=[];
      building.findList(params2).then(function (result) {
        let x = result.data
        if (x) {
          x.forEach(item => {
            self.options_build.push({
              buildingid: item.id,
              buildingname: item.buildingname
            });
          });
        }
      });



    },

    search2: function() {
            let self = this;
            let params1 = {
                orgid: this.$route.query.id
            };
            API.findListByOrgan(params1).then(function(result) {
                
                let x = result.data;
                if (x) {
                  x.forEach(item => {
                    self.jammerListByOrg.push({
                      id: item.id,
                      isequal: item.isequal

                    });
                  });
                };
                console.log("push后的数组对象：",self.jammerListByOrg)
                self.jammerListByOrg.forEach(item => {

                console.log("push后的isequal：",item.isequal)
                if(item.isequal!=12){
                  console.log("这个id为：",item.id);
                  self.start2(item.id)
                }
              })
                //self.jammerListByOrg = [];
              });
    },


    search3: function() {
            let self = this;
            let params2 = {
                orgid: this.$route.query.id
            };
            API.findListByOrgan(params2).then(function(result) {
                
                let x = result.data;
                if (x) {
                  x.forEach(item => {
                    self.jammerListByOrg3.push({
                      id: item.id,
                      isequal: item.isequal

                    });
                  });
                };
                console.log("push后的数组对象：",self.jammerListByOrg3)
                self.jammerListByOrg3.forEach(item => {

                console.log("push后的isequal：",item.isequal)
                if(item.isequal!=12){
                  console.log("这个id为：",item.id);
                  self.close2(item.id)
                }
              })
                //self.jammerListByOrg = [];
              });
    },

    showJammerDetail(x) {
      this.$router.push({
        path: "/jammer/detail",
        query: {
            id: x,
            organname:this.$route.query.organname
           
          }
      });
    },

    //一键开启
    start: function () {
        console.log("过滤框的id是：",this.filters.buildingid)
        let that = this;
        let params = {
          orgid : this.$route.query.id,
          buildingid : this.filters.buildingid,
          status : 1
          
        };
        this.$confirm('确认开启屏蔽器吗?', '提示', {type: 'warning'}).then(() => {
          that.loading = true;
          API.start(params).then(function (result) {
            that.loading = false;
            if (result) {
                //that.isstart=true;
                //that.$message.success({showClose: true, message: '开启成功', duration: 1500});
                that.search(); 
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            
          });

          // setTimeout(() => {
          //   this.start2();
          //   //console.log('定时开启')
          // }, 5000)
          //console.log("定时开启",that.jammerList);


          setTimeout(() => {

              
              
              that.jammerListByOrg=[];
              that.search2();
              


              // that.jammerListByOrg.forEach(item => {

              //   console.log(item)
              //   // if(item.isequal!=12){
              //   //   console.log("这个id为：",item.value);
              //   //   that.start2(item.value)
              //   // }
              // })
              //console.log("定时开启",that.jammerListByOrg);
            //   that.jammerListByOrg.forEach(item => {
            //     console.log(item.isequal2)
            //     if(item.isequal2!=12){
            //       console.log("这个id为：",item.value2);
            //       that.start2(item.value2)
            //     }
            // })
              //console.log("定时开启",that.jammerList);
              //console.log("定时开启",that.jammerListByOrg);
            //   that.jammerListByOrg.forEach(it => {
            //     console.log(it.isequal2)
            //     // if(item.isequal2!=12){
            //     //   console.log("这个id为：",item.value2);
            //     //   that.start2(item.value2)
            //     // }
            // })
           
          }, 2000)

          // that.jammerList.forEach(item => {
          //   console.log(item.isequal)
          //   if(item.isequal!=12){
          //     console.log("这个id为：",item.value);
          //     that.start2(item.value)
          //   }
          // })

          
        }).catch(() => {
        });

        

      },

      // start2: function(jammerid){
      //   console.log("传过来的id为：",jammerid);

      // },


      //单个开启
      start2: function (jammerid) {
        let that = this;
        let params = {
          jammerid : jammerid,
          buildingid : this.filters.buildingid,
          status : 1
        };
        
          that.loading = true;
          API.singlestart(params).then(function (result) {
            that.loading = false;
            if (result) {
              //that.$message.success({showClose: true, message: '开启成功', duration: 1500});
              that.search();
            }
          })
     
      },


      //一键开启
    // start2: function () {
    //     let that = this;
    //     let params = {
    //       orgid : this.$route.query.id,
    //       status : 1
          
    //     };
        
    //       that.loading = true;
    //       API.start(params).then(function (result) {
    //         that.loading = false;
    //         if (result) {
    //             that.search(); 
    //         }
    //       })
          
    //   },


    //一键关闭
    close: function () {
        let that = this;
        let params = {
          orgid : this.$route.query.id,
          buildingid : this.filters.buildingid,
          status : 0
          
        };
        this.$confirm('确认关闭屏蔽器吗?', '提示', {type: 'warning'}).then(() => {
          that.loading = true;
          API.start(params).then(function (result) {
            that.loading = false;
            if (result) {
                //that.isstart=false;
                //that.$message.success({showClose: true, message: '关闭成功', duration: 1500});
                that.search(); 
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            
          });

          setTimeout(() => {
              that.jammerListByOrg3=[];
              that.search3();
           }, 2000)


        }).catch(() => {
        });

        

      },


      //单个关闭
      close2: function (jammerid) {
        let that = this;
        let params = {
          jammerid : jammerid,
          buildingid : this.filters.buildingid,
          status : 0
        };
        
          that.loading = true;
          API.singlestart(params).then(function (result) {
            that.loading = false;
            if (result) {
              //that.$message.success({showClose: true, message: '开启成功', duration: 1500});
              that.search();
            }
          })
     
      },




    //   //一键关闭
    // close2: function (jammerid) {
    //     let that = this;
    //     let params = {
    //       orgid : jammerid,
    //       status : 0
          
    //     };
    //       that.loading = true;
    //       API.start(params).then(function (result) {
    //         that.loading = false;
    //         if (result) {
    //             that.search(); 
    //         }
    //       })

    //   },

     toggle () {
        this.$refs['fullscreen'].toggle() // recommended
        // this.fullscreen = !this.fullscreen // deprecated
      },
      fullscreenChange (fullscreen) {
        this.fullscreen = fullscreen
      }




  },

  
};
