        //line-height: 1.5rem;
        color: #fff;
        font-size: 0.5rem;
        //text-overflow: ellipsis;
        
        white-space: nowrap;
        
      }
      .name {
        font-size: 1rem;
        font-weight: bold;
      }
      .name,
      .manager,
      .status,
