/**
 * 
 * 保存到定时相关api
 */
import * as API from './'

export default {


  findList: params => {
    return API.POST('/time/list', params)
  },
  add: params => {
    return API.POST(`/time/add`, params)
  },
  dingshi: params => {
    return API.POST(`/time/dingshi`, params)
  },
  del: params => {
    return API.POST(`/time/del`, params)
  }
  

}



